import { Subject, Observable } from "rxjs";
import { Injectable } from "@angular/core";

@Injectable()
export class LeadService {

  private _step = new Subject<string>();
  private _opened = new Subject<boolean>();

  public readonly step: Observable<string> = this._step.asObservable();
  public readonly opened: Observable<boolean> = this._opened.asObservable();

  changeStep(text: string) {
    this._step.next(text);
  }

  changeOpened(opened: boolean) {
    this._opened.next(opened);
  }
}