import { Injectable } from '@angular/core';
import { DeviceDetectorService } from 'ngx-device-detector';
@Injectable({
    providedIn: 'root'
})
export class DeviceService {
    public deviceInfo = null;
    public isDeviceMobile: boolean;
    public isDeviceTablet: boolean;
    public isDeviceDesktop: boolean;
    public isDeviceSmall: boolean;
    public dimensionExt: string;

    constructor(private deviceService: DeviceDetectorService) {
        this.deviceInfo = this.deviceService.getDeviceInfo();
        this.isDeviceMobile = this.deviceService.isMobile();
        this.isDeviceDesktop = this.deviceService.isDesktop();
        this.isDeviceTablet = this.deviceService.isTablet();
    }
    isMobile() {
        return this.isDeviceMobile;
    }
    isTablet() {
        return this.isDeviceTablet;
    }
    isDesktop() {
        return this.isDeviceDesktop;
    }
    isSmallDevice() {
        if (this.isDeviceMobile && window.innerWidth < 350) {
            this.isDeviceSmall = true;
        }
        else {
            this.isDeviceSmall = false;
        }
        return this.isDeviceSmall;
    }

    screenTypeBySize() {
      if (window.innerWidth < 539) this.dimensionExt = 'mobile';
      if (window.innerWidth >= 540 && window.innerWidth < 992) this.dimensionExt = 'tablet';
      if (window.innerWidth > 992) this.dimensionExt = 'web';

      return this.dimensionExt;
    }

}
