import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError } from 'rxjs';
//import { TimeService } from './time.service';
import { tap, catchError } from 'rxjs/operators';
//import { MainSharedService } from 'src/app/util/shared.service';
import { API_BE_V3 } from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})

export class MediaService {

  constructor(
    private http: HttpClient,
    //private time: TimeService,
    //private shared: MainSharedService
  ) { }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || "Server Error");
  }

  getMedia(categorie) {
    const url = API_BE_V3 + "/media/" + categorie;
    return this.http.get<any>(url).pipe(tap(data => catchError(this.errorHandler)));
  }
}
