import CryptoJS from 'crypto-js'
import { environment } from 'src/environments/environment';

export class Crypter {

    static key = environment.ZEROKM_SALT

    static cryptoJSAesJson = {
        stringify: (cipherParams: any) => {
            let j: any = { ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64) };
            if (cipherParams.iv) j.iv = cipherParams.iv.toString();
            if (cipherParams.salt) j.s = cipherParams.salt.toString();
            return JSON.stringify(j);
        },
        parse: (jsonStr: any) => {
            let j: any = JSON.parse(jsonStr);
            let cipherParams: any = CryptoJS.lib.CipherParams.create({ ciphertext: CryptoJS.enc.Base64.parse(j.ct) });
            if (j.iv) cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv)
            if (j.s) cipherParams.salt = CryptoJS.enc.Hex.parse(j.s)
            return cipherParams;
        }
      }

    static encrypter(data, mode = 'AES'){
        switch (mode) {
            case 'AES':        
            default:
                return this.aes(data, 'encrypt');
        }
    }

    static decrypter(data, mode = 'AES'){
        switch (mode) {
            case 'AES':        
            default:
                return this.aes(data, 'decrypt');
        }
    }

    static aes(data, type='encrypt'){
        if(type === 'encrypt'){
            return CryptoJS.AES.encrypt(JSON.stringify(data), this.key,  { format: this.cryptoJSAesJson }).toString()
        } else if(type == 'decrypt'){
            return CryptoJS.AES.decrypt(JSON.stringify(data), this.key,  { format: this.cryptoJSAesJson }).toString(CryptoJS.enc.Utf8); 
        }
    }


    
}
