import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-button-content',
  templateUrl: './button-content.component.html',
  styleUrls: [
    './button-content.component.scss',
    './../../card-prod/card-prod-find/card-prod-find.component.scss',
  ]
})
export class ButtonContentComponent implements OnInit {

  classButton: string = '';

  @Input() color: string = 'primary-700';
  @Input() size: string = 'normal';
  @Input() text: string = '';
  @Input() icon: string = '';
  @Input() iconPosition: string = '';
  @Input() url: string = '';
  @Input() route: string = '';
  @Input() noPadding: boolean = false;

  constructor() { }

  ngOnInit(): void {
    this.classButton = 'button-' + this.color + ' button-' + this.size
  }
}
