import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { AlertService } from 'src/app/core/services/alert.service';
import { MainSharedService } from 'src/app/util/shared.service';

@Injectable()
export class UnauthorizedInterceptor implements HttpInterceptor {

  constructor(
    private router: Router,
    private alert: AlertService,
    private shared: MainSharedService
  ) { }

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      catchError(err => {
        if (err.status === 401 && !request.url.match(/auth\/login/)) {
          this.shared.user = null;
          this.shared.setUsuarioLogado();
          this.shared.setLoadingAtivo(false);
          // this.router.navigate(['/login', { redirectTo: window.location.pathname }], { queryParamsHandling: 'merge' });
          this.router.navigate(['/'], { queryParamsHandling: 'merge' });
          this.alert.open({
            tipo: 'message',
            mensagem: 'Sua sesso expirou. Faa login novamente.',
            botao: 'Ok',
            titulo: 'Ateno!'
          }, true);
          return;
        }
        // const error = err.error.message || err.statusText;
        return throwError(err);
      })
    );
  }
}
