import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';
import { MovidaHeaderService } from './movida-header.service';
import { UserService } from 'src/app/core/services/user.service';

import { environment } from 'src/environments/environment';
import { HeaderMenuItem } from './movida-header.types';

import { menuItems } from './mock-items';

@Component({
  selector: 'movida-header',
  templateUrl: './movida-header.component.html',
  styleUrls: ['./movida-header.component.scss']
})
export class MovidaHeaderComponent implements OnInit {

  @Output() doLogout = new EventEmitter();

  public authenticated: boolean = false
  public user: string = '';
  public isSeller: boolean = false;
  public sellerLink: string = '';
  public username: string = '';
  public isSellerProfile: boolean = false;
  public profileInfo: string = 'Desligado';
  public menuItems: Array<HeaderMenuItem> = menuItems;
  public movidaURL: string = environment.siteMovida;
  public editProfileLink: string = `${this.movidaURL}usuario/cadastro`;

  constructor(
    private sharedService: MainSharedService,
    private headerService: MovidaHeaderService,
    private userService: UserService
  ) { }

  ngOnInit(): void {
    this.sharedService.usuarioLogado.subscribe(user => {
      if (user) {
        this.setAuthenticatedUser();

        if(user.nomeUsuario === null)
          this.logout();
      }
    });

    setTimeout(() => {
      this.headerService.fetchMenuItems().subscribe(data => {
         this.menuItems = data['data'];
      });
    }, 3000);
    this.setAuthenticatedUser();
  }

  forceCloseMenuMobile() {
    document.querySelectorAll('.m-menu__checkbox').forEach((element: HTMLInputElement) => {
      element.checked = false;
    })

    document.querySelector('body').style.overflow = '';
  }

  setAuthenticatedUser() {
    const user = JSON.parse(localStorage.getItem('_user'));

    if (user && user.razaosocial) {
      this.authenticated = true;
      this.user = user.razaosocial.split(' ')[0];
      this.isSeller = this.sharedService.isSeller;
      this.username = this.sharedService.linkPermanent || user.VUsername;
      this.sellerLink = `${location.origin}/em/${this.username}`;

      this.isSellerProfile = this.isSeller && this.sharedService.choosenView === 'seller';
    }
  }


  copyLink() {
    const elem = document.createElement('textarea');
    elem.style.position = 'fixed';
    elem.style.left = '0';
    elem.style.top = '0';
    elem.style.opacity = '0';
    elem.value = this.sellerLink;
    document.body.appendChild(elem);
    elem.focus();
    elem.select();
    document.execCommand('copy');
    document.body.removeChild(elem);
  }

  saveUsername(event) {
    this.userService.saveUsername(event.target.value).then((response: any) => {
      this.username = event.target.value;
      this.sharedService.linkPermanent = response.data.username;
    });
  }

  changeUserProfile(isSeller: boolean) {
    this.sharedService.choosenView = isSeller ? 'seller' : 'public';
    this.isSellerProfile = isSeller;
  }


  changeMenuVisibility(event) {
    const menuVisible = event.currentTarget.checked;
    const body = document.querySelector('body');

    if (menuVisible) {
      body.style.overflow = 'hidden';
    } else {
      body.style.overflow = '';
      window.setTimeout(this.forceCloseMenuMobile, 500);
    }
  }

  logout() {
    this.doLogout.emit();
    this.authenticated = false;
    this.user = '';
  }

  callLogin() {
    const url = this.movidaURL;
    this.headerService.reserveToken().subscribe({
      next(response: any) {
        if (response['data']) {
          const token = response['data'];
          const loginURL = `${url}login/zkm/${token}`;

          document.location.href = loginURL;
        }
      },
      complete() {
        console.log('auth');
      },
      error(msg) {
        console.log('Error => ', msg);
      }
    });
  }
}
