import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class MovidaHeaderService {


  private tokenEndpoint = '/api/v1/remoteAuth';
  private menuEndpoint = '/api/v3/header-config/getall';

  constructor(private http: HttpClient) { }

  reserveToken(): Observable<any[]> {
    const url = environment.API_SYSTEM + this.tokenEndpoint;
    return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
  }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || 'Server Error');
  }

  fetchMenuItems(): Observable<any[]> {
    const url = environment.API_SYSTEM + this.menuEndpoint;
    return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
  }
}
