import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
//  HttpClientModule,
  HttpInterceptor
} from '@angular/common/http';
import { Observable, Subscription } from 'rxjs';
import { MainSharedService } from 'src/app/util/shared.service';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  token: any;
  subscription: Subscription;

  constructor(private shared: MainSharedService) { }

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    
    let reqClone = null;

    // Add authorization header with jwt token if available
    if (this.shared.user && this.shared.user.access_token && !req.url.match(/chaordicsystems/)) {
      reqClone = req.clone({
        headers: req.headers.set('UserToken', this.shared.user.access_token)
      });
    }
    return next.handle(reqClone ? reqClone : req);
  }
}
