import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {throwError as observableThrowError} from 'rxjs';
import {tap, catchError} from 'rxjs/operators';

/** Recibos V3 */
const API_BE_V3 = '/api/v3';
const API_RECIBO_DECODE = API_BE_V3 + '/payments/recibo/decode/';
const API_RECIBO_PDF = API_BE_V3 + '/payments/recibo/pdf/';

@Injectable({
  providedIn: 'root'
})

export class PagamentosReciboService {

  constructor(private http: HttpClient) {

  }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || 'Server Error');
  }

  getReciboDecoded(token) {
    const url = this.getAPISystemURL() + API_RECIBO_DECODE + token;
    return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
  }

  getReciboPdf(token) {
    const url = this.getAPISystemURL() + API_RECIBO_PDF + token;
    return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
  }

  getAPISystemURL() {
    const origin = window.location.origin;
    if (origin.indexOf('localhost') > -1) {
      return '';
    }
    if (origin.indexOf('0km-xp.dev') > -1) {
      return 'https://be-zkm-xp.dev.movidacloud.com.br';
    }
    if (origin.indexOf('0km-xp.hmg') > -1) {
      return 'https://be-zkm-xp.hmg.movidacloud.com.br';
    }
    if (origin.indexOf('0km-vs.dev') > -1) {
      return 'https://be-zkm-vs.dev.movidacloud.com.br';
    }
    if (origin.indexOf('0km-cn.dev') > -1) {
      return 'https://be-zkm-cn.dev.movidacloud.com.br';
    }
    if (origin.indexOf('0km.dev') > -1) {
      return 'https://be-zkm.dev.movidacloud.com.br';
    }
    return 'https://be-zkm.movidacloud.com.br';
  }
}
