import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { CartaoCreditoNovoModel } from 'src/app/shared/models/cartao-credito-novo.model';
import { LoggedUser } from 'src/app/shared/models/client.model';
//import { MainSharedService } from 'src/app/util/shared.service';
import { API_CHECKOUT_CARD_LIST } from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})
export class CartoesConsultaService {

  constructor(
    private http: HttpClient,
    //private mainService: MainSharedService
  ) { }

  public cartaoDados = new BehaviorSubject({} as CartaoCreditoNovoModel);

  public getCartoes(): Observable<any> {
    return this.http.get<LoggedUser>(`${API_CHECKOUT_CARD_LIST}`)
  }

}
