import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  userAuth: boolean = false;

  constructor() { }

  authUser(user) {
    this.save(user);
    if (user.success == true) {
      this.userAuth = true;
    } else {
      this.userAuth = false;
    }
  }

  userConfirmAuth() {
    const user = JSON.parse(localStorage.getItem('userAplication'));
    if (user !== null) {
      return this.getUserToken().length > 0;
    }
  }

  private save(user) {
    localStorage.setItem('userAplication', JSON.stringify({
      user: user,
      user_token: user.access_token
    }));
  }

  public getUserToken() {
    const token = JSON.parse(localStorage.getItem('userAplication'));
    return token.user_token;
  }

  public getUser() {
    const userSession = JSON.parse(localStorage.getItem('userAplication'));
    return userSession.user;
  }

}
