import { Injectable } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';

export type TypeGTM = 'home' | 'header' | 'search' | 'order' | 'checkout' | 'seller';

@Injectable({
  providedIn: 'root'
})
export class GtmService {

  constructor(
    private shared: MainSharedService
  ) { }

  private getEventCategory(type: TypeGTM) {
    switch (type) {
      case 'header': return 'Motor_Header';
      case 'home': return 'Motor_Home';
      case 'order': return 'Pedido';
      case 'checkout': return 'Checkout';
      case 'seller': return 'Vendedor';
      default: return 'Category';
    }
  }

  private getEventAction(type: TypeGTM) {
    switch (type) {
      case 'search': return 'Motor_Busca';
      case 'order': return 'Criacao_Pedido';
      case 'checkout': return 'Pagamento_Pedido';
      case 'seller': return 'Pre_Pedido';
      default: return 'Action';
    }
  }

  addSearchGTM(categoryType: TypeGTM, actionType: TypeGTM, text) {
    if (text) {
      this.shared.addGTM({
        'Event Category': this.getEventCategory(categoryType),
        'Event Action': this.getEventAction(actionType),
        'Event Label': `${text}`
      });
    } else {
      this.shared.addGTM({
        'Event Category': this.getEventCategory(categoryType),
        'Event Action': this.getEventAction(actionType),
        'Event Label': 'Busca_Completa'
      });
    }
  }

  addResultsGTM() {
    this.shared.addGTM({
      'Event Category': 'Home',
      'Event Action': 'Quero_Contratar',
      'Event Label': 'Quero_Contratar_Home'
    });
  }

  addPageGTM(eventType, page, title) {
    this.shared.addGTM({
      'event': eventType,
      'url_pagina': page,
      'title_pagina': title
    });
  }

  addSearchFilterGTM(filterType, filter) {
    this.shared.addGTM({
      'Event Category': 'Resultados',
      'Event Action': 'Filtro',
      'Event Label': 'Selecao_Filtro',
      'filtro': filter,
      'tipo_filtro': filterType
    });
  }

  addChosenVehicleGTM(marca, modelo, valor, isOnDemand) {
    this.shared.addGTM({
      'Event Category': 'Home',
      'Event Action': 'Oferta',
      'Event Label': 'Vitrine',
      'marca': marca,
      'modelo': modelo,
      'periodo': '24',
      'valor': valor,
      'produto': '0km',
      'tipo_produto': isOnDemand ? 'Sob Encomenda' : 'Pronta Entrega'
    });
  }

  addOrderGTM(type: TypeGTM, isOnDemand, orderId, carDetails) {
    const products = [];
    const protecoes = [];
    const protecoesExtras = [];
    const servicosAdicionais = [];

    if (carDetails.protection && Object.keys(carDetails.protection).length > 0) {
      // Adicionando protecoes no campo products do data layer de reserva
      products.push({
        'name': carDetails.protection.nome,
        'price': '0.00',
        'category': 'Protecao',
        'quantity': '1'
      });
      protecoes.push({
        'name': carDetails.protection.nome,
        'price': '0.00',
        'category': 'Protecao',
        'quantity': '1'
      });
    }

    for (let i = 0; i < carDetails.extraProtections.length; i++) {
      // Adicionando protecoes extras no campo products do data layer de reserva
      if (carDetails.extraProtections[i] && Object.keys(carDetails.extraProtections[i]).length > 0) {
        products.push({
          'name': carDetails.extraProtections[i].nome,
          'price': '0.00',
          'category': 'Protecao Extra',
          'quantity': '1'
        });
        protecoesExtras.push({
          'name': carDetails.extraProtections[i].nome,
          'price': '0.00',
          'category': 'Protecao Extra',
          'quantity': '1'
        });
      }
    }

    // Adicionando servios adicionais no campo products do data layer de reserva
    for (let i = 0; i < carDetails.additionalServices.length; i++) {
      if (carDetails.additionalServices[i] && Object.keys(carDetails.additionalServices[i]).length > 0) {
        products.push({
          'name': carDetails.additionalServices[i].nome,
          'price': '0.00',
          'category': 'Servico Adicional',
          'quantity': '1'
        });
        servicosAdicionais.push({
          'name': carDetails.additionalServices[i].nome,
          'price': '0.00',
          'category': 'Servico Adicional',
          'quantity': '1'
        });
      }
    }

    products.push({
      'name': carDetails.groupId,
      'price': '0.00',
      'category': 'Grupo',
      'quantity': '1'
    });

    products.push({
      'name': isOnDemand ? 'Sob Encomenda' : 'Pronta Entrega',
      'price': '0.00',
      'category': 'Modalidade',
      'quantity': '1'
    });

    this.shared.addGTM({
      'Event Category': this.getEventCategory(type),
      'Event Action': this.getEventAction(type),
      'Event Label': 'Pedido_0km',
      'produto': '0km',
      'tipo_produto': isOnDemand ? 'Sob Encomenda' : 'Pronta Entrega',
      'data_checkout': carDetails.bookingPayload ? carDetails.bookingPayload.pickup_date : null,
      'data_devolucao': carDetails.bookingPayload ? carDetails.bookingPayload.return_date : null,
      'periodo': `${carDetails.period.meses} meses`,
      'franquia': `${carDetails.mileage.valor} kms`,
      'loja': carDetails.pickupStore ? carDetails.pickupStore.Loja : null,
      'grupo_veiculo': carDetails.groupId,
      'opcionais': {
        protecoes: protecoes,
        protecoesExtras: protecoesExtras,
        servicosAdicionais: servicosAdicionais
      },
      'ecommerce': {
        'purchase': {
          'actionField': {
            'id': orderId,
            'revenue': parseFloat(parseFloat(carDetails.values.total).toFixed(2)),
          },
          'products': products
        }
      }
    });
  }
}
