import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, pluck } from 'rxjs/operators';
import { Lead } from '../types/lead';

@Injectable()
export class LeadApiService {
  private urlConfig: string = ''
  private urlSubmit: string = ''

  constructor(
    @Inject('environment')
    environment: any,
    private http: HttpClient
  ) {
    this.urlConfig = `${environment.API_SYSTEM}/api/v1/lead-config`;
    this.urlSubmit = `${environment.API_SYSTEM}/api/v2/lead`;
  }

  fetchLeadOptions(): Observable<any> {
    return this.http.get<any>(this.urlConfig).pipe(
      pluck('data'),
    );
  }

  submitLead(data: Lead): Observable<any> {
    return this.http.post<any>(this.urlSubmit, data).pipe(
      catchError((error: HttpErrorResponse) => {
        return throwError('Erro ao enviar o formulrio')
      })
    )
  }
}