import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { menuItems } from './mock-items';
import { FooterMenuItem, FooterMenuSubitem } from './movida-footer.types';

@Component({
  selector: 'movida-footer',
  templateUrl: './movida-footer.component.html',
  styleUrls: ['./movida-footer.component.scss']
})
export class MovidaFooterComponent implements OnInit {
  
  public menuItems: Array<FooterMenuItem> = menuItems;

  constructor(
    private router: Router
  ) {  }

  ngOnInit(): void {
  }

  sendBrands(brandId: number) {
    const data = { 'key': 'brand', 'MarcaID': brandId };
    localStorage.setItem('keySearch', JSON.stringify(data));
    return this.router.navigate(['/assinatura/busca']);
  }

  public sendCategories(categoryName: string, route: string) {
    const data = { 'key': 'category', 'name': categoryName };
    localStorage.setItem('keySearch', JSON.stringify(data));
    return this.router.navigate([route]);
  }

  // remover esse mtodo e usar o [routerLink] quando a tela de listagem for refeita
  navigate(event: MouseEvent, menu: FooterMenuSubitem) {
    if (menu.brandId > 0) {
      return this.sendBrands(menu.brandId);
    }

    if (menu.route.indexOf('/categoria') > -1) {
      return this.sendCategories(menu.name, menu.route);
    }

    this.router.navigate([menu.route]);
  }
}
