import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import {
  API_FILTERS_VALUES
} from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})
export class MenuService {

  changeMenu = new EventEmitter<string>();

  private _lojas: string  = 'assets/mock/lojas.json';
  private _brands: string  = 'assets/mock/brands.json';
  private tokenEndpoint = '/api/v1/remoteAuth';

  constructor(private http: HttpClient) { }

  getLojas(): Observable<any[]> {
    return this.http.get<any[]>(this._lojas).pipe(tap(data => catchError(this.errorHandler)));
  }

  getBrands(): Observable<any[]> {
    return this.http.get<any[]>(this._brands).pipe(tap(data => catchError(this.errorHandler)));
  }

  reserveToken(): Observable<any[]> {
    const url = environment.API_SYSTEM + this.tokenEndpoint;
    return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
  }

  getInfo(token) {
    const url = environment.API_SYSTEM + this.tokenEndpoint + '/' + token;
    return this.http.get(url);
  }

  getFilters() {
    return this.http.get<any>(`${API_FILTERS_VALUES}`).pipe(tap(data => catchError(this.errorHandler)));
  }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || 'Server Error');
  }
}
