import {Component, OnInit, Input, OnChanges, SimpleChanges} from '@angular/core';

@Component({
  selector: 'movida-bandeiras-cartao',
  templateUrl: './bandeiras-cartao.component.html',
  styleUrls: ['./bandeiras-cartao.component.scss']
})
export class BandeirasCartaoComponent implements OnInit, OnChanges {

  @Input() BandeiraID: number;
  MASTERCARD = false;
  AMEX = false;
  DINERS = false;
  CABAL = false;
  BANESCARD = false;
  CREDZ = false;
  VISA = false;
  ELO = false;
  HIPER = false;
  HIPERCARD = false;
  JCB = false;
  MAESTRO = false;
  MAIS = false;
  REDESHOP = false;
  SOROCRED = false;
  ELECTRON = false;
  VRAUTO = false;

  constructor() {
    this.findCardFlag(this.BandeiraID);
  }

  ngOnChanges(changes: SimpleChanges) {
    this.findCardFlag(this.BandeiraID);
  }

  ngOnInit(): void {
  }

  findCardFlag(BandeiraID: number) {
    const flags = {
      8: 'MASTERCARD',
      1: 'AMEX',
      4: 'DINERS',
      14: 'CABAL',
      12: 'BANESCARD',
      13: 'BANESCARD',
      16: 'CREDZ',
      9: 'VISA',
      10: 'ELO',
      11: 'HIPER',
      7: 'HIPERCARD',
      20: 'JCB',
      2: 'MAESTRO',
      15: 'MAIS',
      5: 'REDESHOP',
      17: 'SOROCRED',
      3: 'ELECTRON',
      21: 'VRAUTO'
    };
    if (typeof flags[this.BandeiraID] !== 'undefined') {
      this[flags[this.BandeiraID]] = true;
    }
  }
}
