/* Angular */
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, ErrorHandler, LOCALE_ID, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { StorageServiceModule } from 'ngx-webstorage-service';
import { CommonModule, registerLocaleData } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatTabsModule } from '@angular/material/tabs';
import { MatSelectModule } from '@angular/material/select';
import { MatRadioModule } from '@angular/material/radio';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatCardModule } from '@angular/material/card';
import { MatButtonModule } from '@angular/material/button';
import { MatListModule } from '@angular/material/list';
import { MatInputModule } from '@angular/material/input';
import { MatDialogModule } from '@angular/material/dialog';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';

import { APP_BASE_HREF } from '@angular/common';
import { environment } from 'src/environments/environment';

/* Dependencies */
import { NgxMaskModule } from 'ngx-mask';

/* Modules */
import { AppRoutingModule } from './app-routing.module';
import { SharedModule } from './shared/shared.module';
import { IconsModule } from './icons/icons.module';

/* Services */
import { MainSharedService } from './util/shared.service';
import { LoginService } from 'src/app/core/services/login.service';
import { CarouselService } from 'src/app/core/services/carousel.service';
import { AuthService } from 'src/app/core/services/auth.service';
import { RouteGuardService } from 'src/app/core/guards/route-guard.service';
import { OrderService } from 'src/app/core/services/order.service';
import { AlertService } from 'src/app/core/services/alert.service';
import { WindowService } from 'src/app/util/window.service';

/* Interceptors */
import { TokenInterceptor } from 'src/app/core/interceptors/token.interceptor';
import { ChaordicInterceptor } from 'src/app/core/interceptors/chaordic.interceptor';
import { UnauthorizedInterceptor } from 'src/app/core/interceptors/unauthorized.interceptor';

/* Pages */
import { AppComponent } from './app.component';
import { StampComponent } from './pages/home/stamp/stamp.component';
import { TermosPayerComponent } from './pages/confirm-payer/termos-payer/termos-payer.component';
import { BtnRedirectAccountComponent } from './pages/confirm/btn-redirect-account/btn-redirect-account.component';
import { GlobalMessage, AlertMessageComponent } from './shared/alert-message/alert-message.component';
import { LiveChatComponent } from './shared/whatsapp/live-chat/live-chat.component';
import { CartaoCreditoCadastradoModule } from './shared/cartao-credito-cadastrado';
import { CartaoCreditoNovoFrenteModule } from './shared/cartao-credito-novo-frente';
import { CartaoCreditoNovoVersoModule } from './shared/cartao-credito-novo-verso';

import { LoginButtonComponent } from './shared/login-button/login-button.component';
import { WhatsappIconComponent } from './shared/whatsapp/whatsapp-icon/whatsapp-icon.component';
import { LoaderComponent } from './shared/loader/loader.component';
import { LeadModule, MovidaHeaderModule } from 'projects/zkm-components/src/public-api';
import { MovidaFooterModule } from 'projects/zkm-components/src/lib/components/movida-footer/movida-footer.module';

/* Web Components */
import 'movida-portal-component';
import 'movida-fines-component';
import 'movida-my-payments';
import 'movida-crlv-digital';

/* Handlers */
import { getErrorHandler } from './handlers/custom-error-handler';
import { ServiceWorkerModule } from '@angular/service-worker';
import localePt from '@angular/common/locales/pt';
import { ComponentsModule } from 'projects/zkm-components/src/lib/components/components.module';


/* Global variables */
registerLocaleData(localePt);
const USERNAME: string = localStorage.getItem('username') ? `/em/${localStorage.getItem('username')}/` : '';

@NgModule({
  declarations: [
    AppComponent,
    LiveChatComponent,
    AlertMessageComponent,
    GlobalMessage,
    StampComponent,
    TermosPayerComponent,
    BtnRedirectAccountComponent,
    LoginButtonComponent,
    WhatsappIconComponent,
    LoaderComponent,
  ],
  imports: [
    CommonModule,
    IconsModule,
    CartaoCreditoCadastradoModule,
    CartaoCreditoNovoFrenteModule,
    CartaoCreditoNovoVersoModule,
    MatRadioModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    MatCardModule,
    MatButtonModule,
    MatIconModule,
    MatTabsModule,
    MatSelectModule,
    MatCheckboxModule,
    MatTabsModule,
    MatListModule,
    MatInputModule,
    MatDialogModule,
    MatSlideToggleModule,
    NgxMaskModule.forRoot(),
    HttpClientModule,
    SharedModule,
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: true }),
    StorageServiceModule,
    MovidaHeaderModule,
    LeadModule, 
    MovidaFooterModule,   
    ComponentsModule
  ],
  exports: [GlobalMessage],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  providers: [
    { provide: ErrorHandler, useFactory: getErrorHandler },
    { provide: LOCALE_ID, useValue: 'pt-BR' },
    { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ChaordicInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: UnauthorizedInterceptor, multi: true },
    { provide: APP_BASE_HREF, useValue: USERNAME },
    { provide: 'environment', useValue: environment },
    RouteGuardService,
    AuthService,
    MainSharedService,
    LoginService,
    CarouselService,
    OrderService,
    AlertService,
    WindowService
  ],
  entryComponents: [GlobalMessage],
  bootstrap: [AppComponent]
})
export class AppModule { }
