import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { API_ORDER, API_LOCK_VEHICLE, API_SELLER_LINK } from 'src/app/core/api/app.api';
import { OrderService } from './order.service';

@Injectable({
  providedIn: 'root'
})
export class SellerService {

  private defaultUtm = {
    utm_source: '',
    utm_medium: '',
    utm_campaign: '',
    utm_content: '',
    utm_term: '',
  }

  constructor(
    private http: HttpClient,
    private order: OrderService
  ) { }

  lockVehicle(vehiclePlate) {
    return this.http.get<any>(`${API_LOCK_VEHICLE}/${vehiclePlate}`);
  }

  getSellerLink(isOnDemand, user, vehicle, tokenVendedor) {
    return this.http.post<any>(`${API_ORDER}/seller/save`, this.order.getOrderPayload(isOnDemand, user, vehicle, tokenVendedor, this.defaultUtm));
  }

  createSellerLink(token) {
    return this.http.post<any>(`${API_SELLER_LINK}`, { payload: token })
  }

  recoverSellerLink(hash) {
    return this.http.get<any>(`${API_SELLER_LINK}/${hash}`)
  }

  createOrderBySellerLink(token) {
    return this.http.post<any>(`${API_ORDER}/seller`, { token: token });
  }

  getByUsername(username) {
    return this.http.get<any>(`${API_SELLER_LINK}/username/${username}`);
  }
}
