import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from "@angular/router";
import { MainSharedService } from 'src/app/util/shared.service';
import { AlertService } from 'src/app/core/services/alert.service';

@Injectable()
export class RouteGuardService implements CanActivate {

  constructor(
    private router: Router,
    private shared: MainSharedService,
    private alert: AlertService
  ) { }

  canActivate(snapRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | Promise<boolean> {


    const goToHome = () => this.router.navigate(['/'], { queryParamsHandling: 'merge' });

    // Non logged area
    if (['/login', '/alterar-senha', '/recuperar-senha', '/confirmar-cadastro'].indexOf(state.url) >= 0) {
      if (this.shared.user) {
        goToHome();
        this.showAlert(
          'Voc j est logado!',
          'Para acessar a pgina solicitada  preciso sair de sua conta.'
        );
        return false;
      }
    }

    // Routes that need to be logged in and trying to update registration
    if (['/alterar-cadastro', '/completar-cadastro'].indexOf(state.url) >= 0) {
      if (!this.shared.user) {
        goToHome();
        this.showAlert(
          'Voc no est logado!',
          'Para atualizar as informaes de sua conta faa o login.'
        );
        return false;
      }
    }

    // Routes that need to be logged in and don't have complete registration
    if (['/assinatura/pagamento', '/assinatura/capturar-foto', '/assinatura/enviar-cnh'].indexOf(state.url) >= 0) {
      if (!this.shared.user) {
        goToHome();
        this.showAlert(
          'Voc no est logado!',
          'Para prosseguir com a compra faa o login.'
        );
        return false;
      } else if (!this.shared.user.cadastro_completo) {
        this.router.navigate(['/completar-cadastro'], { queryParamsHandling: 'merge' });
      }
    }

    // Routes that need to be logged in and isn't a seller
    if (['/vendedor/link'].indexOf(state.url) >= 0) {
      if (!this.shared.user) {
        goToHome();
        this.showAlert(
          'Voc no est logado!',
          'Para prosseguir com a compra faa o login.'
        );
        return false;
      } else if (!this.shared.user.vendedor_autorizado) {
        this.router.navigate(['/']);
        return false;
      }
    }

    // Routes that need to be logged in and go back to route
    if (['/vendedor/pedido'].indexOf(state.url) >= 0) {
      if (!this.shared.user) {
        //const url = state.url.charAt(0) === '/' ? state.url.substring(1) : state.url;
        // this.router.navigate(['/', { redirectTo: url }], { queryParamsHandling: 'merge' });
        goToHome();
        this.showAlert(
          'Voc no est logado!',
          'Para prosseguir com a compra faa o login.'
        );
        return false;
      } else if (!this.shared.user.cadastro_completo) {
        this.router.navigate(['/completar-cadastro'], { queryParamsHandling: 'merge' });
      }
    }

    // Router that need to be logged in
    if (['/assinatura/pedido', '/minha-conta', '/minha-conta/pedido'].indexOf(state.url) >= 0) {
      if (!this.shared.user) {
        goToHome();
        this.showAlert(
          'Voc no est logado!',
          'Para prosseguir com a compra faa o login.'
        );
        return false;
      }
    }

    return true;
  }

  showAlert(title: string, text: string) {
    this.alert.open({
      tipo: 'message',
      mensagem: text,
      botao: 'Ok',
      titulo: title
    }, true);
  }

}
