import { Component, OnInit } from '@angular/core';
import { LeadApiService } from './services/lead-api.service';
import { LeadService } from './services/lead.service';
import { MainSharedService } from 'src/app/util/shared.service';
declare const window: any;

@Component({
  selector: 'zkm-lead',
  templateUrl: './lead.component.html',
  styleUrls: ['./lead.component.scss', './elements/top-menu/top-menu.component.scss'],
})
export class LeadComponent implements OnInit {

  public step: string = '';
  public opened: boolean = false;
  public options: Array<object> = [];
  public enabled: boolean = false;
  public isSellerLink: boolean = false;
  public buttonText = 'Fale com um consultor';

  constructor(
    private leadService: LeadService,
    private leadApiService: LeadApiService,
    private sharedService: MainSharedService
  ) {
    if (this.sharedService.verifyIsSellerLink() || sessionStorage.getItem('vendedorID')) {
      this.isSellerLink = true;
    }
  }

  ngOnInit() {
    if (this.isSellerLink) {
      return;
    }

    setTimeout(() => {

      this.setupLiveChat();

      this.leadApiService.fetchLeadOptions().subscribe(data => {
        if (data.lead) {
          const button = data.lead.filter((item) => item.type === 'button')[0]

          if (button && button.show) {
            this.options = data.lead.filter((l) => l.show && l.type !== 'button')
            this.buttonText = button.title;
            this.enabled = true;
          }
        }
      });
    }, 5000);


    window.addEventListener('resize', this.calculateHeight);
    this.calculateHeight();

    this.leadService.step.subscribe((step) => {
      this.step = step;
    })
    this.leadService.opened.subscribe((opened) => {
      this.opened = opened;
    })
  }

  calculateHeight() {
    const docElement = document.documentElement;
    docElement.style.setProperty('--app-height', `${window.innerHeight}px`);
  }

  setupLiveChat() {
    // integrao zoss (https://wb.zoss.com.br/movida/carrozero/chat/scr_chat.js)
    function S4() {
      return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }
    const license_id = 8788896;  // LICENA DO CHAT
    const group_id = 15;    //GRUPO DO CHAT
    let chat_id = 'C'+group_id+Date.now().toString() + (S4() + S4() + S4() + "4" + S4().substr(0, 3) + S4() + S4() + S4() + S4()).toLowerCase();
    chat_id = chat_id.substring(0, 32);

    window.__lc = window.__lc || {};
    window.__lc.license = license_id;
    window.__lc.group = group_id;
    window.__lc.params = [
      { name: 'Token', value: chat_id }
    ];
    // end integrao zoss

    ;(function(n,t,c){function i(n){return e._h?e._h.apply(null,n):e._q.push(n)}var e={_q:[],_h:null,_v:"2.0",on:function(){i(["on",c.call(arguments)])},once:function(){i(["once",c.call(arguments)])},off:function(){i(["off",c.call(arguments)])},get:function(){if(!e._h)throw new Error("[LiveChatWidget] You can't use getters before load.");return i(["get",c.call(arguments)])},call:function(){i(["call",c.call(arguments)])},init:function(){var n=t.createElement("script");n.async=!0,n.type="text/javascript",n.src="https://cdn.livechatinc.com/tracking.js",t.head.appendChild(n)}};!n.__lc.asyncInit&&e.init(),n.LiveChatWidget=n.LiveChatWidget||e}(window,document,[].slice))

    window.LiveChatWidget.call('hide');
    window.LiveChatWidget.on('ready', () => {
      window.LC_API.hide_chat_window()
      window.LC_API.on_chat_window_minimized = function() {
        window.LC_API.hide_chat_window()
      };
    })
  }

  open(): void {
    this.leadService.changeOpened(true)
    this.leadService.changeStep('menu')
  }

  close(): void {
    this.leadService.changeOpened(false)
  }
}
