  private loadingAtivoSource = new BehaviorSubject<boolean>(null);
  loader$ = this.loadingAtivoSource.asObservable();

  setCounterLoading(active: boolean) {
    const counter = localStorage.getItem('loaderCounter');
    let loaderCounter = counter ? parseInt(counter, 10) : 0;
    if (active) {
      localStorage.setItem('loaderCounter', (loaderCounter + 1).toString());
      this.loadingAtivoSource.next(true);
    } else if (loaderCounter > 1) {
      localStorage.setItem('loaderCounter', (loaderCounter - 1).toString());
    } else {
      localStorage.setItem('loaderCounter', '0');
      this.loadingAtivoSource.next(false);
    }
  }

