  // Modo debug
  public modeDebug(type: string, title: string, data: any) {

    if (environment.MODE_DEBUG && !environment.production) {

      const data_type = Array.isArray(data) ? 'array' : typeof (data)
      const data_value = data_type === 'string' ? `"${data}"` : data;
      const data_title = `[${title}] => `;
      const css = "background: #F97D00; color: white; height: 70px; padding: 5px; font-weight: 500; margin: -2px; border-radius: 3px";
      const css2 = "background: #000; color: white; height: 70px; padding: 5px; font-weight: 500; margin: -2px; border-radius: 3px";
      console.log("%c[MODE_DEBUG]%s", css, `MOVIDA ALUGUEL DE CARROS`);
      console.log(`%cDataType: [${data_type}]`, css2);
      switch (type) {
        case 'info':
          console.info(data_title, data_value)
          break;
        case 'log':
          console.log(data_title, data_value)
          break;
        case 'table':
          console.table(data_title, data_value)
          break;
        case 'error':
          console.error(data_title, data_value)
          break;
        default:
          break;
      }
      return
    }

  }

