  getSrcImage(fipe, medias, screen='') {
    if(medias === undefined || medias === '' || medias === []){
      return ;
    }

    let  media = medias.filter(
      res => (res.titulo === fipe)
    );

    if(media.length){
      if (screen.length > 0 && (screen == '/' || screen.includes('busca'))) {
        return media[0].src_vitrine;
      } else if (screen.length >0 && (screen.includes('pagamento') || screen.includes('sucesso'))) {
        return media[0].src_pagamento;
      } else {
        return media[0].src;
      }
    } else {
      return 'assets/img/vehicles/carro-nao-encontrado.png';
    }
  }

  private b64toByteArray(b64Data: any, sliceSize: number = 512): any[] {
    const byteCharacters = atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);
      const byteNumbers = new Array(slice.length);

      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }

    return byteArrays;
  }

  private b64toFile(
    b64Data: any,
    contentType: string = '',
    filename: string | null = null,
    sliceSize: number = 512
  ): File {
    const fileExtension = '.pdf';

    if (!filename) {
      filename = `file_${Math.floor(Math.random() * 10000)}${fileExtension}`;
    }

    return new File(
      this.b64toByteArray(b64Data, sliceSize),
      filename,
      { type: contentType }
    );
  }

  downloadPdf(base64String, fileName) {
    return new Promise<void>((res, rej) => {
      try {
        const file = this.b64toFile(base64String, 'application/pdf', `${fileName}.pdf`);
        const fileUrl = window.URL.createObjectURL(file);
        const wname = 'wname' + Math.floor(Math.random() * 10000);
        const tab = window.open(
          fileUrl,
          wname,
          'fullscreen=1,toolbar=0,location=0,directories=0,status=0,menubar=0,scrollbars=1,resizable=0'
        );
        tab.moveTo(0, 0);
        tab.onload = () => tab.onbeforeunload = () => {
          window.URL.revokeObjectURL(fileUrl)
        };

        res();
      } catch (error) {
        rej(error);
      }
    });
  }
