import { Inject, Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { pluck } from 'rxjs/operators';
import { ApiBanner, Banner, ComponentBanner } from './banner-carousel.types';

type ApiResponse = {
  success: boolean,
  data: Array<ApiBanner>
}

@Injectable()
export class BannerCarouselService {
  private url: string = ''

  constructor(
    @Inject('environment')
    environment: any,
    private http: HttpClient
  ) {
    this.url = `${environment.API_SYSTEM}/api/v3/media/banners-image`;
  }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || "Server Error");
  }

  normalizeBanner(banner: ApiBanner): Banner {
    return {
      clickable: banner.clickable,
      src: banner.src,
      target: banner.target,
      title: banner.titulo,
      type: banner.type,
      url: banner.url,
      interval: banner.interval
    }
  }

  groupByTitle(banners: Banner[]): Array<Array<Banner>> {
    const key = 'title';
    return Object.values(banners.reduce(
      (result, item) => ({
        ...result,
        [item[key]]: [
          ...((result as any)[item[key]] || []),
          item,
        ]
      }),
      {},
    ))
  }

  separateBannerTypes(banners: Banner[]): ComponentBanner {
    const mobile = banners.filter(x => x.type === 'mobile')[0];
    const web = banners.filter(x => x.type === 'web')[0];
    const tablet = banners.filter(x => x.type === 'tablet')[0];
    return { web, mobile, tablet };
  }

  groupBanners(banners: ApiBanner[]): ComponentBanner[] {
    const groupedBanners = this.groupByTitle(banners.map(this.normalizeBanner))

    return groupedBanners.map(this.separateBannerTypes);
  }

  fetch(): Observable<ApiBanner[]> {
    return this.http.get<ApiResponse>(this.url).pipe(
      pluck('data'),
    );
  }
}
