import { Component, OnInit, Inject, HostListener, Output, EventEmitter } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { DeviceService } from 'src/app/core/services/device.service';
import { Router } from '@angular/router';
import { MainSharedService } from 'src/app/util/shared.service';
import { OrangeCheckBoldComponent } from 'src/app/icons/orange-check-bold.component';

@Component({
  templateUrl: 'message.html',
  styleUrls: ['./alert-message.component.scss'],
  providers:[OrangeCheckBoldComponent],
})
export class GlobalMessage {

  dialogTitle: string;
  dialogText: string;
  @Output() close = new EventEmitter();
  @Output() yes = new EventEmitter();
  @Output() not = new EventEmitter();
  @Output() terms = new EventEmitter();

  constructor(
    @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<GlobalMessage>,
    private router: Router,
    public dialog: MatDialog,
    public shared: MainSharedService,
    //private orangeCheckBold: OrangeCheckBoldComponent,
  ) { }

  @HostListener('window:keyup.esc') onKeyUp() {
    this.dialogRef.close();
    this.not.emit('false')
  }

  clearDialog() {
    this.dialog.closeAll();
  }

  closeDialog() {
    this.dialogRef.close()
    this.close.emit('close')
  }

  yesAccepted() {
    this.dialogRef.close()
    this.yes.emit('true')
  }

  notAccepted() {
    this.dialogRef.close()
    this.not.emit('false')
  }

  redirectToHome() {
    this.dialog.closeAll();
    this.router.navigate(['/']);
  }

  redirectTo(url) {
    this.dialog.closeAll();
    this.router.navigate([url]);
  }

  openRegionModal() {
    this.dialog.closeAll();
    this.shared.setOpenRegionModal(true);
  }
}

@Component({
  selector: 'ng-alert-message',
  templateUrl: './alert-message.component.html',
  styleUrls: ['./alert-message.component.scss']
})
export class AlertMessageComponent implements OnInit {

  isMobile: boolean;

  constructor(
    public dialog: MatDialog,
    private deviceService: DeviceService
  ) { }

  ngOnInit() {
    this.isMobile = this.deviceService.isMobile();
  }

  openDialog(parametros, tipo, disableClose): Promise<MatDialogRef<GlobalMessage>> {
    parametros.botao = parametros.botao ? parametros.botao : 'OK';
    parametros.tipo = tipo;
    parametros.isMobile = parametros.isMobile ? parametros.isMobile : this.isMobile;
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve(this.dialog.open(GlobalMessage, {
          width: '100vw',
          data: parametros,
          disableClose: disableClose
        }));
      });
    });
  }

  clearDialog() {
    this.dialog.closeAll();
  }
}
