import { MainSharedService } from 'src/app/util/shared.service';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { LeadApiService } from '../services/lead-api.service';
import { LeadService } from '../services/lead.service';
import { MovidaCPFValidatorService } from '../../../services/movida-cpf-validator.service';
import { Lead, UTM } from '../types/lead';
import { MovidaLocalStorage } from '../../../services/movida-local-storage.service';

@Component({
  selector: 'zkm-lead-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {

  @Input() headerText: string = '';

  public alertTitle: string = '';
  public alertDescription: string = '';
  public showAlert: boolean = false;

  public contactOption: string = 'whatsapp';

  public loading: boolean = false;

  public isSelected: boolean = false;
  public maxTextAreaCharacteres: number = 200;

  private successSubmit = false;

  private defaultUtm: UTM = {
    utm_source: '',
    utm_medium: '',
    utm_campaign: '',
    utm_content: '',
    utm_term: '',
  }

  private userData: Lead = {
    name: '',
    email: '',
    phone: '',
    cpf: '',
    message: '',
    contactType: 'whatsapp',
    utm: this.defaultUtm,
  }

  leadForm: FormGroup;
  controlName: FormControl = new FormControl();
  controlEmail: FormControl = new FormControl();
  controlPhone: FormControl = new FormControl();
  controlCpf: FormControl = new FormControl();
  controlMessage: FormControl = new FormControl();

  constructor(
    private leadService: LeadService,
    private leadApiService: LeadApiService,
    private cpfValidatorService: MovidaCPFValidatorService,
    private sharedService: MainSharedService,
    private fb: FormBuilder,
    private localStorage: MovidaLocalStorage,
  ) {}

  ngOnInit() {
    this.createFormValidators();
    this.createForm();
    this.setFormValues();

    this.sharedService.userWatch().subscribe(userData => {
      if (userData) {
        this.userData.name = userData.razaosocial;
        this.userData.email = userData.email;
        this.userData.cpf = userData.cpf;
      }
      this.setFormValues();
    });
  }
  createFormValidators() {
    this.controlName.setValidators([
      Validators.required,
    ]);

    this.controlEmail.setValidators(
      [
        Validators.required,
        Validators.email
      ],
    );

    this.controlPhone.setValidators(
      [
        Validators.required,
        Validators.minLength(11),
      ],
    );
    this.controlCpf.setValidators(
      [
        Validators.required,
        this.cpfValidatorService.isValidCpf
      ],
    );
    this.controlMessage.setValidators(
      [
        Validators.maxLength(this.maxTextAreaCharacteres),
      ]
    );
  }

  setFormValues() {
    this.controlName.setValue(this.userData.name);
    this.controlEmail.setValue(this.userData.email);
    this.controlPhone.setValue('');
    this.controlCpf.setValue(this.userData.cpf);
    this.controlMessage.setValue(this.userData.message);
  }

  createForm() {
    this.leadForm = this.fb.group({
      name: this.controlName,
      email: this.controlEmail,
      phone: this.controlPhone,
      cpf: this.controlCpf,
      message: this.controlMessage
    });
  }

  onSubmit() {
    if (this.leadForm.valid) {
      this.loading = true
      const formValues = this.leadForm.value;

      let utmData = this.defaultUtm;
      if (this.localStorage.get('utm')) {
        utmData = JSON.parse(this.localStorage.get('utm'));
      }
      const lead: Lead = {
        name: formValues.name,
        email: formValues.email,
        phone: formValues.phone,
        cpf: formValues.cpf,
        message: formValues.message,
        contactType: this.contactOption,
        utm: utmData
      }

      this.leadApiService.submitLead(lead).subscribe((result) => {
        this.alertTitle = 'Mensagem enviada com sucesso!'
        this.alertDescription = 'Pronto, assim que possvel um de nossos consultores entrar em contato contigo para lhe ajudar!'
        this.successSubmit = true;
        this.clearForm();
      }, (error) => {
        this.alertTitle = 'Erro ao enviar!'
        this.alertDescription = 'Erro ao enviar o formulrio. Por favor tente novamente ou escolha outra forma de contato.'
      }).add(() => {
        this.showAlert = true;
        this.loading = false
      })
    }
  }

  clearForm(): void {
    this.leadForm.reset();
    this.isSelected = false;
  }

  close(): void {
    this.leadService.changeOpened(false);
  }

  back(): void {
    this.leadService.changeStep('menu');
  }

  onCloseAlert(): void {
    if (this.successSubmit) {
      this.close();
    }

    this.showAlert = false;
  }

  openLegalInfo(event): void {
    event.preventDefault();
    this.leadService.changeStep('privacy');
  }

  clickContactType(type: string): void {
    this.contactOption = type;
  }
}
