import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ClientRegister } from 'src/app/shared/models/client.model';
import { MainSharedService } from 'src/app/util/shared.service';
import { SellerService } from './seller.service';
import {
  API_REGISTER_USER,
  API_UPDATE_USER,
  API_CEP,
  API_SAVE_FEEDBACK,
  API_USER,
  API_UPDATE_PASSWORD,
  API_RECOVER_PASSWORD,
  API_VALIDATE_CPF,
  API_MY_SELLER,
  API_MY_POINTS,
  API_SELLER_USERNAME,
  API_VERIFY_ACCOUNT,
  API_MY_FINES,
  API_MY_FINES_FILE
} from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private username;

  constructor(
    private http: HttpClient,
    private shared: MainSharedService,
    private seller: SellerService
    ) { }

  getUserAddress(user) {
    const addresses = user.pessoa ? user.pessoa.enderecos : null;
    const address = addresses ? addresses.filter(address => address.tipo_endereco.nome === 'Residencial')[0] : null;
    if (address) {
      const logradouro = address.logradouro ? `${address.logradouro} ` : '';
      const endereco = address.endereco ? `${address.endereco} ` : '';
      const numero = address.numero ? `${address.numero} ` : '';
      const complemento = address.complemento ? `${address.complemento}, ` : ', ';
      const bairro = address.bairro ? `${address.bairro} - ` : '';
      const cidade = address.cidade && address.cidade.nome ? `${address.cidade.nome}/` : '';
      const uf = address.cidade && address.cidade.estado && address.cidade.estado.uf ? `${address.cidade.estado.uf} - ` : '';
      const cep = address.cep ? `CEP ${address.cep}` : '';
      return `${logradouro}${endereco}${numero}${complemento}${bairro}${cidade}${uf}${cep}`;
    } else {
      return null;
    }
  }

  getUser() {
    let _user = localStorage.getItem('_user');
    let accessToken: any;
    accessToken = JSON.parse(_user).accessToken;

    const header = {
      headers: new HttpHeaders().set('Authorization', `Bearer ${accessToken}`)
    }
    return this.http.get<any>(API_USER, header);
  }

  getCEP(cep: string): Observable<any> {
    return this.http.get<any>(`${API_CEP}/${cep}`);
  }

  validateCPF(cpf): Observable<any>{
    const httpBody = {
      'cpf_rf': cpf
    };
    return this.http.post<any>(`${API_VALIDATE_CPF}`, httpBody);
  }

  sendRespFinanceiro(rf): Observable<any>{
    return this.http.post<any>(`${API_VALIDATE_CPF}`, rf);
  }

  registerUser(user: ClientRegister): Observable<any> {
    const formData = new FormData();
    formData.append('cpf', user.cpf);
    formData.append('nome', user.name);
    formData.append('data_nascimento', user.birthdate);
    formData.append('sexo', user.gender);
    formData.append('email', user.email);
    formData.append('telefone', user.tel);
    formData.append('celular', user.cel);
    formData.append('fax', null);
    formData.append('rg', user.rg);
    formData.append('cnh_nro', user.cnhNumber);
    formData.append('cnh_validade', user.cnhExpiration);
    formData.append('cnh_categoria', user.cnhCategory);
    formData.append('endereco_cep', user.cep);
    formData.append('endereco_logradouro', user.address);
    formData.append('endereco_numero', user.addressNumber);
    formData.append('endereco_complemento', user.addressComplement);
    formData.append('endereco_ibge_cidade', user.ibgeCode);
    formData.append('senha', '000000');
    formData.append('confirma_sms', '0');
    formData.append('link_confirmacao', 'http://www.movida.com.br/confirma.php?codigo=');
    formData.append('municipio_natural_ibge', user.naturalIbgeCode);
    return this.http.post<any>(`${API_REGISTER_USER}`, formData);
  }

  updateUser(user): Observable<any> {
    let _user = localStorage.getItem('_user');
    let userToken: any;
    userToken = JSON.parse(_user).accessToken;

    const url = API_UPDATE_USER;
    let reqHeader = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + userToken
    });

    return this.http.put<any>(url, user, { headers: reqHeader });
  }

  saveFeedback(feedback: any): Observable<any> {
    return this.http.post<any>(`${API_SAVE_FEEDBACK}`, feedback);
  }

  recoverPassword(cpf, tipo) {
    const data = {
      cpf: cpf,
      tipo: tipo
    };
    return this.http.post<any>(`${API_RECOVER_PASSWORD}`, data);
  }

  updatePassword(cpf, codigoaltsenha, senha) {
    const data = {
      cpf: cpf,
      codigoaltsenha: codigoaltsenha,
      senha: senha
    };
    return this.http.post<any>(`${API_UPDATE_PASSWORD}`, data);
  }

  async linkPermanent(username) {
    this.username = username;
    localStorage.setItem('username', this.username);
    
    let seller = await this.seller.getByUsername(this.username).toPromise();
    seller.data !== null ? this.shared.vendedorID = seller.data.VendedorID : null;
  }

  checkSeller():any {
    const _user = localStorage.getItem('_user');
    const _username = localStorage.getItem('username') !== 'undefined' ? localStorage.getItem('username'): '' ;

    if(this.username || _username) {
      if (!_user) {
        return false
      }
      if (_user) {
        return {
          username: _username,
          pessoaID: JSON.parse(_user).PessoaID ? JSON.parse(_user).PessoaID : '',
          vendedorPessoaID: JSON.parse(_user).VendedorPessoaID,
        };
      } else {
        return false;
      }
    }
    return false;
  }

  getPoints() {
    return this.http.get<any>(`${API_MY_POINTS}`);
  }

  getFinesInfo(multaId?: String) {
    return this.http.get<any>(`${API_MY_FINES}` + (multaId ? '/' + multaId : ''));
  }

  getFinesBase64File(multaId: String) {  
    return this.http.get<any>(`${API_MY_FINES_FILE}` + (multaId ? '/' + multaId : ''));
  }

  verifyAccount() {
    return this.http.get<any>(`${API_VERIFY_ACCOUNT}`);
  }

  async mySeller() {
    const check = this.checkSeller();

    if (!check) {
      this.shared.isSellerLink = false
      return
    }
 
    if(check && check.vendedorPessoaID == "") {
      const data = {
        PessoaID: check.pessoaID,
        Username: check.username,
      }
      
      let mySeller = await this.http.post<any>(`${API_MY_SELLER}`, data).toPromise();

      if(mySeller) {
        let user = this.shared.user
        user.VendedorPessoaID = mySeller.data.PessoaID
        this.shared.user = user
        this.shared.isSellerLink = true
      } else {
        this.shared.isSellerLink = true
      }
    } else {
      this.shared.isSellerLink = true
    }
  }

  async salveUsername(username:string)
  {
     this.saveUsername(username);
  }

  async saveUsername(username: string) {
    const _user = localStorage.getItem('_user');
    const data = {
      pessoaID: JSON.parse(_user).PessoaID,
      username: username,
    }
    return await this.http.post<any>(`${API_SELLER_USERNAME}`, data).toPromise();
  }
}