import { Renderer2, Inject, Injectable } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { WindowService } from './window.service';

@Injectable({
  providedIn: 'root'
})
export class CookieService {

  public SCRIPT_COOKIE_PATH = 'https://cdn-prod.securiti.ai/consent/cookie-consent-sdk.js';

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private winService: WindowService
  ) {
  }

  public loadJsScript(renderer: Renderer2): void {
    setTimeout(() => {
      let script = renderer.createElement('script');
      script.type = 'text/javascript';

      script.setAttribute('data-tenant-uuid','cb225976-cf9d-4523-b9cd-5cd46c44bda3');
      script.setAttribute('data-domain-uuid','eaf437b9-c411-4e76-93d9-c0767fa17e21');
      script.setAttribute('data-backend-url', 'https://app.securiti.ai');

      script.id = '5cd46c44bda3';
      script.async = true;
      script.defer = true;
      script.src = this.SCRIPT_COOKIE_PATH;
      script.defer = true;
      renderer.appendChild(this.document.body, script);

      setTimeout(()=>{
        this.winService.nativeWindow.initCmp();
      }, 5000);
    }, 5000);
  }

  public handlerCookie() {
    setTimeout(()=>{
      this.addAndRemoveCookieClass()
    }, 8000)
  }

  public addAndRemoveCookieClass () {

    const btnFaleVendedor = document.getElementById('btn-vendedor')
    const div = document.getElementsByClassName('cc-window')

    if(Array.from(div).length > 0){
      btnFaleVendedor.classList.add('btn-chat')

      document.getElementsByClassName('cc-btn')[0].addEventListener("click", function()  {

        btnFaleVendedor.classList.remove('btn-chat')
      }, false )
    }else{
      this.handlerCookie()
    }

  }

}
