import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DownloadPdfService {

  constructor() {
  }

  private b64toByteArray(b64Data: any, sliceSize: number = 512): any[] {
    const byteCharacters = atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);
      const byteNumbers = new Array(slice.length);

      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }

    return byteArrays;
  }

  private b64toFile(
    b64Data: any,
    contentType: string = '',
    filename: string | null = null,
    sliceSize: number = 512
  ): File {
    const fileExtension = '.pdf';

    if (!filename) {
      filename = `file_${Math.floor(Math.random() * 10000)}${fileExtension}`;
    }

    return new File(
      this.b64toByteArray(b64Data, sliceSize),
      filename,
      {type: contentType}
    );
  }

  downloadPdf(base64String, fileName) {
    return new Promise<void>((res, rej) => {
      try {
        const file = this.b64toFile(base64String, 'application/pdf', `${fileName}.pdf`);
        const fileUrl = window.URL.createObjectURL(file);
        const tab = document.createElement('a');
        tab.setAttribute('href', fileUrl);
        tab.setAttribute('target', '_blank');
        tab.style.visibility = 'none';
        tab.click();
        res();
      } catch (error) {
        rej(error);
      }
    });
  }

  getLinkPdf(base64String, fileName) {
    return new Promise((res, rej) => {
      try {
        const file = this.b64toFile(base64String, 'application/pdf', `${fileName}.pdf`);
        res(window.URL.createObjectURL(file));
      } catch (error) {
        rej(error);
      }
    });
  }

  isIOS() {
    return [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod'
      ].includes(navigator.platform)
      // iPad on iOS 13 detection
      || (navigator.userAgent.includes("Mac") && "ontouchend" in document)
  }

}
