import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import Splide from '@splidejs/splide';
import { BannerCarouselService } from './banner-carousel.service';
import { ComponentBanner } from './banner-carousel.types';
import { mockBanners } from './mock-banners';

@Component({
  selector: 'zkm-banner-carousel',
  templateUrl: './banner-carousel.component.html',
  styleUrls: ['./banner-carousel.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class BannerCarouselComponent implements OnInit {
  public banners: ComponentBanner[] = mockBanners;
  public splide : any = null;

  constructor(
    private service: BannerCarouselService,    
  ) {  }

  ngOnInit(): void {
    setTimeout(() => this.initializeSplide(), 0);
    this.service.fetch()
      .subscribe(banners => {
        this.banners = this.service.groupBanners(banners);
        this.banners.forEach((banner) => {
          banner.web.interval = banner.web.interval * 1000;
          banner.mobile.interval = banner.mobile.interval * 1000;
          banner.tablet.interval = banner.tablet.interval * 1000;
        });

        this.refrehSplide();
      });
  }

  refrehSplide() {
    this.splide.remove(0);
    setTimeout(() => this.splide.refresh(), 0);
  }

  initializeSplide() {
    this.splide = new Splide('.splide', {
      type: 'loop',
      focus: 'center',
      autoWidth: true,
      autoplay: true,
      interval: 40000,
      perMove: 1,
      flickPower: 50,
      flickMaxPages: 1,
      rewind: true,
      breakpoints: {
        1130: {
          arrows: false
        }
      }
    }

    ).mount();
    let pagination0List = document.getElementsByClassName('pagination-banner-0');
    for (let i = 0; i < pagination0List.length; i++) {
      pagination0List[i].classList.add('is-active');
    }

    this.splide.on('move', function (newIndex, prevIndex) {
      let itensAdd = document.getElementsByClassName('pagination-banner-' + newIndex);
      for (let i = 0; i < itensAdd.length; i++) {
        itensAdd[i]['classList'].add('is-active');
      }

      let itensRemove = document.getElementsByClassName('pagination-banner-' + prevIndex);
      for (let i = 0; i < itensRemove.length; i++) {
        itensRemove[i]['classList'].remove('is-active');
      }
    });
  }
}
