import { Brand } from './movida-brands.types';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { catchError, pluck } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class MovidaBrandsService {

  private brandsEndpoint: string = '/api/v2/fleet/filter?home=true';

  constructor(private http: HttpClient) { }

  errorHandler(error: HttpErrorResponse) {
    return observableThrowError(error.message || 'Server Error');
  }

  fetchBrands(): Observable<Array<Brand>> {
    const url = environment.API_SYSTEM + this.brandsEndpoint;
    return this.http.get<Array<Brand>>(url).pipe(
      catchError(this.errorHandler),
      pluck('data', 'brands'),
    );
  }
}
