import { Component, Input, OnInit, HostListener } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";

import { SearchBarService } from "./search-bar.service";
import { MediaService } from 'src/app/core/services/media.service';
import { MainSharedService } from 'src/app/util/shared.service';

@Component({
  selector: 'movida-header-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss']
})
export class MovidaSearchBarComponent implements OnInit {
  term: string = '';

  @Input() placeholder: string = ''
  @Input() route: string = ''

  veiculos : any = [];
  veiculosFilter: any = [];

  medias: any [];

  constructor(
    private router: Router,
    private activedRoute: ActivatedRoute,
    private searchBarService: SearchBarService,
    private mediaService: MediaService,
    private shared: MainSharedService,
  ) { }

  async ngOnInit() {
    await this.mediaService.getMedia('fipes-image').subscribe(res => {
      this.medias = res.data;
    });

    this.getVehicles();
  }

  @HostListener('document:click', ['$event.target']) onClick(event) {
    let search_list = document.getElementById('search_list');
    if (!search_list.contains(event)) {
      search_list.classList.remove('search_list_active');
      document.getElementById('overlay_search').classList.remove('overlay_active');
    }

    let searchInput = document.getElementById('searchInput');
    if (searchInput.contains(event) && this.term.length > 0) {
      search_list.classList.add('search_list_active');
      document.getElementById('overlay_search').classList.add('overlay_active');
    }
  }

  clearSearch() {
    this.term = '';

    this.activedRoute.queryParams.subscribe(params => {
      if (params['terms']) {
        // causa um reload na pgina
        // tela de veculos no atualiza sozinha se o termo buscado  vazio
        document.location.href= '/assinatura/busca';
      }
    })
  }

  async getVehicles() {
    const pickup_date = this.getPickupDate();
    const return_date = this.getReturnDate();
    const pickup_store = "380";
    const return_store = "380";

    this.veiculos = await this.searchBarService.getShowcaseV2(pickup_date, return_date, pickup_store, return_store)
    .subscribe(response => {
      this.veiculos = response.data.VehAvailRSCore.VehVendorAvails.VehVendorAvail.VehAvails;
    })
  }

  getPickupDate() {
    let today = new Date();
    today.setDate(today.getDate() + 1);
    return today.toLocaleDateString('pt-BR') + ' 23:59';
  }

  getReturnDate() {
    let currentDate = new Date();
    let daysToAdd = 1441;
    currentDate.setDate(currentDate.getDate() + daysToAdd );
    return currentDate.toLocaleDateString('pt-BR') + ' 23:59';
  }

  getSrcImage(fipe) {
    return this.shared.getSrcImage(fipe, this.medias, this.router.url);
  }

  search() {
    if (this.term) {
      document.getElementById('search_list').classList.remove('search_list_active');
      document.getElementById('overlay_search').classList.remove('overlay_active');
      window.location.href=`${window.location.origin}${this.route}?terms=${this.term}`;
    }
  }

  completeSearch(busca) {
    if(busca.length > 0) {
      this.veiculosFilter = this.autoComplete(busca);
    }
  }

  autoComplete(busca) {
    return this.veiculos.filter((veiculo) => {
      let veiculoNameMinusculo = veiculo.VehAvail.VehAvailCore.Vehicle.VehMakeModel.Name.toLowerCase();
      let veiculoDescriptionMinusculo = veiculo.VehAvail.VehAvailCore.Vehicle.Description.toLowerCase();
      let buscaMinusculo = busca.toLowerCase();

      return veiculoNameMinusculo.includes(buscaMinusculo) || veiculoDescriptionMinusculo.includes(buscaMinusculo);
    })
  }

  editCarModel(model, fipe) {
    let model_edited = model.toLowerCase().replace(/,?\s+/g, '-').replace(/[^\w-]+/g, '');
    return `/assinatura/detalhes/${model_edited}/${fipe}`;
  }
}
