import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';
// modules
import { Component } from '@angular/core';
//import { Router } from '@angular/router';
import Splide from '@splidejs/splide';

// services
import { MovidaBrandsService } from './movida-brands.service';

// types
import { Brand } from './movida-brands.types';

@Component({
  selector: 'movida-brands',
  templateUrl: './movida-brands.component.html',
  styleUrls: ['./movida-brands.component.scss']
})
export class MovidaBrandsComponent {

  public brands: Array<Brand> = [];
  public isLoadingBrands: boolean = true;

  @Output() contentLoaded: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    private brandsMovidaService: MovidaBrandsService,
    //private router: Router,
  ) { }

  startFetchBrands(): void {
    this.brandsMovidaService.fetchBrands().subscribe(data => {
      this.brands = data;
      setTimeout(() => this.initializeSplide(), 0);
      this.isLoadingBrands = false;
      this.contentLoaded.emit(true);
    });
  }

  initializeSplide() {
    new Splide('.splide_brands', {
      type: 'loop',
      perMove: 1,
      pagination: false,
      autoWidth: true,
    }).mount();
  }

  sendBrands(brand) {
    console.log(brand);
    /* const data = { 'key': 'brand', 'MarcaID': brandID };
    localStorage.setItem('keySearch', JSON.stringify(data));
    this.router.navigate(['/assinatura/busca']); */
  }
}
