import {Component, OnInit, AfterViewInit, HostListener, Renderer2} from '@angular/core';
import {MainSharedService} from './util/shared.service';
import {Router, NavigationEnd} from '@angular/router';
import {API_BE_V1, API_BE_V3} from 'src/app/core/api/app.api';
import {DomSanitizer} from '@angular/platform-browser';
import {environment} from 'src/environments/environment';
import {GtmService} from 'src/app/core/services/gtm.service';
import {CheckLoginService} from './core/services/checklogin.service';
import {LoginButtonComponent} from './shared/login-button/login-button.component';
import {AlertMessageComponent} from './shared/alert-message/alert-message.component';
import { LocalStorage } from './util/local-storage.service';

import { CookieService } from './util/cookie.service'

import * as Sentry from "@sentry/browser";
//import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  providers: [AlertMessageComponent, LoginButtonComponent]
})
export class AppComponent implements OnInit, AfterViewInit {

  @HostListener('document:visibilitychange', ['$event'])
  visibilitychange() {
    if (!document.hidden && !this.shared.user && this.shared.token && this.shared.loginClicked) {
      this.login.getLogin(this.shared.token)
    }
  }

  public gtm = this.sanitizer.bypassSecurityTrustResourceUrl(environment.gtm);

  isMobile: boolean;
  hideForCheckout: boolean = false;

  public apiEndpoint = API_BE_V1;
  public disableRegionSelect: boolean = true;
  public enableToggleHeaderSearch: boolean = false;
  private enableChat: boolean = true;
  public apiEndpointThree = API_BE_V3;

  //Access by iPhone
  public urlIOS: string

  public actualPageYOffset = window.pageYOffset;
  public oldPageYOffset = window.pageYOffset;

  public noHeader: boolean
  public noChat: boolean

  

  constructor(
    public sanitizer: DomSanitizer,
    private router: Router,
    private shared: MainSharedService,
    private login: LoginButtonComponent,
    private gtmS: GtmService,
    private checkLoginService: CheckLoginService,
    private renderer: Renderer2,
    private cookieService: CookieService,
    private storage: LocalStorage,
    // public updates: SwUpdate
  ) {

    router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.checkChatVisibility();

        const urlPath = this.router.url.split('?')[0];
        const urlResponsavel = urlPath.includes('/assinatura/responsavel/pagamento');

        if (urlResponsavel) {
          this.shared.setUrlRespFinan(urlPath)
          this.router.navigate(['/']);
          if (this.shared.user) {
            this.router.navigate([urlPath]);
          } else {
            this.login.remoteLogin();
          }
        }

        /**
         * tratamento de quando vem com a url bugada no ios
         */
        const pathName = decodeURIComponent(this.router.url)
        const regexHaveAuth = `(auth)`;
        const regexGetString = /\/[\w\W]+(auth\/)/;

        if(pathName.match(regexHaveAuth) && pathName.match(regexGetString)){
          let hash = pathName.replace(regexGetString, '')
          this.router.navigate([`auth/${hash}`]);
        }
      }
    });
  }

  ngOnInit() {
    this.cookieService.loadJsScript(this.renderer)

    this.checkLoginService.check()

    this.checkUTMOriginURL();
    this.checkDeviceIsIphone()

    /** CPN = cupom promocional */
    localStorage.setItem('CPN', '');
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      Sentry.init({
        dsn: environment.SENTRY_ANGULAR_DSN,
        release: environment.SENTRY_ANGULAR_RELEASE,
        tracesSampleRate: environment.SENTRY_ANGULAR_SAMPLE_RATE,
      });
    }, 8000);
  }

  ngOnDestroy() {
    Sentry.init({
      enabled: false
    });

    Sentry.close();
  }

  checkUTMOriginURL(): void {

    let processUrl = window.location.href.split('?');
    if (processUrl.length <= 1) {
      return;
    }
    let paramsUrl = {};
    processUrl[1].split('&').map((item) => {
      let values = item.split('=');
      if (['utm_source', 'utm_medium', 'utm_campaign', 'utm_content', 'utm_term'].includes(values[0])){
        paramsUrl[`${values[0]}`] = values[1];
      }
    });
    this.storage.save('utm', paramsUrl);
  }

  searched(event) {
    this.gtmS.addSearchGTM('header', 'search', event);
  }

  routerOutletActivated($event) {
    localStorage.setItem('loaderCounter', '0');

    const disable = $event['disableRegionSelection'] === undefined ? true : $event['disableRegionSelection'];
    this.disableRegionSelect = disable;

    const enableToggle = $event['enableToggleHeaderSearch'] === undefined ? false : $event['enableToggleHeaderSearch'];
    this.enableToggleHeaderSearch = enableToggle;
  }

  logout() {
    this.shared.removeItensLogout()
    this.shared.setUsuarioLogado()
    this.router.navigate(['/']);
  }

  checkDeviceIsIphone() {
    // iPad on iOS 13 detection
    ['iPad Simulator', 'iPhone Simulator', 'iPod Simulator', 'iPad', 'iPhone', 'iPod'].includes(navigator.platform) ||
    (navigator.userAgent.includes("Mac") && "ontouchend" in document) ? this.shared.isIphone = true : this.shared.isIphone = false;
  }

  checkChatVisibility() {
    this.noChat = this.shared.verifyIsSellerLink();

    setTimeout(() => {
      var el = document.getElementById('chat-widget-container');
      if (el !== null) {
        if (this.noChat) {
          el.style.display = 'none';
          this.enableChat = false
        } else if (!this.noChat) {
          el.style.display = 'block';
          this.enableChat = true
        }
      }
    }, 2000);
  }
}
