import { Carousel } from 'src/app/shared/models/carousel.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { API_CAROUSEL, API_LIMIT_CAROUSEL, API_RAND_CAROUSEL, API_APLICATION_NAME } from 'src/app/core/api/app.api';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class CarouselService {

    constructor(
        private http: HttpClient
    ) { }

    getCarousel() {
        return this.http.get<Carousel[]>(
            API_CAROUSEL,
            {
                headers: this.getHeadersCarousel()
            }
        );
    }

    getHeadersCarousel() {
        return new HttpHeaders({
            limit: API_LIMIT_CAROUSEL,
            rand: API_RAND_CAROUSEL,
            aplicationname: API_APLICATION_NAME
        });
    }
}
