import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AgendamentoRetiradaService {

  constructor() { }

  public getWeekDay(day: number): string {

    const weekday = ["Dom", "Seg", "Ter", "Qua", "Qui", "Sex", "Sab"];

    return weekday[day];
  }

  public getMonth(month: number): string {

    const months = ["Janeiro", "Fevereiro", "Maro", "Abril", "Maio", "Junho", "Julho", "Agosto", "Setembro", "Outubro", "Novembro", "Dezembro"];

    return months[month];
  }
}
