import { Component, OnInit } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';

@Component({
  selector: 'ng-loader',
  templateUrl: './loader.component.html',
  styleUrls: ['./loader.component.scss']
})
export class LoaderComponent implements OnInit {
  show = false;

  constructor(private shared: MainSharedService) {}

  ngOnInit() {
    this.shared.loader$.subscribe(res => {
      this.show = res;
    });
  }
}
