import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError as observableThrowError, Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { API_CHAORDIC_SEARCH, API_CHAORDIC_SHOWCASE, API_VITRINE_SHOWCASE, API_VITRINE_MOST_SEARCHED,API_VITRINE_PROMOTIONS_DAY, API_VITRINE_GETALLCARS } from 'src/app/core/api/app.api';

@Injectable({
    providedIn: 'root'
})
export class VitrineService {

    constructor(private http: HttpClient) { }

    errorHandler(error: HttpErrorResponse) {
        return observableThrowError(error.message || "Server Error");
    }

    getShowcaseFilters() {
        const url = API_CHAORDIC_SEARCH + "?terms=1&hide=pagination&hide=queries&applyURL=&sortBy=ascPrice";
        return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
    }

    getShowcaseByRegion(stateFilter, cityFilter) {
        // cidade encomenda id = 1992
        const url = API_CHAORDIC_SEARCH + "?terms=1&" + stateFilter + "&" + cityFilter + "&applyURL=&sortBy=ascPrice";
        return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
    }

    getShowcaseV2(pickup_date, return_date, pickup_store, return_store) {
        const url = API_VITRINE_SHOWCASE + "?data_retirada=" + pickup_date + "&data_devolucao=" + return_date + "&local_retirada=" + pickup_store + "&local_devolucao=" + return_store;
        return this.http.get<any>(url).pipe(tap(data => catchError(this.errorHandler)));
    }

    getCarsMostSearched(pickup_date, return_date, pickup_store, return_store) {
        const url = API_VITRINE_MOST_SEARCHED + "?data_retirada=" + pickup_date + "&data_devolucao=" + return_date + "&local_retirada=" + pickup_store + "&local_devolucao=" + return_store;
        return this.http.get<any>(url).pipe(tap(data => catchError(this.errorHandler)));
    }

	getCarsPromotionsDay(pickup_date, return_date, pickup_store, return_store) {
		const url = API_VITRINE_PROMOTIONS_DAY + "?data_retirada=" + pickup_date + "&data_devolucao=" + return_date + "&local_retirada=" + pickup_store + "&local_devolucao=" + return_store;
		return this.http.get<any>(url).pipe(tap(data => catchError(this.errorHandler)));
	}

    getShowcaseMock(a, b, c, d) {
        return this.http.get<any>('../assets/mock/showcase.json').pipe(tap(data => catchError(this.errorHandler)));
    }

    getRecommendations(source, name): Observable<any[]> {
        const url = API_CHAORDIC_SHOWCASE + "?type=MostVisited&name=" + name + "&source=" + source;
        return this.http.get<any[]>(url).pipe(tap(data => catchError(this.errorHandler)));
    }
    getAllCars(pickup_date, return_date, pickup_store, return_store) {
        const url = API_VITRINE_GETALLCARS + "?data_retirada=" + pickup_date + "&data_devolucao=" + return_date + "&local_retirada=" + pickup_store + "&local_devolucao=" + return_store;
        return this.http.get<any>(url).pipe(tap(data => catchError(this.errorHandler)));
    }

    public sortCarsByPriceASC(cars: Array<any>): Array<any> {

        if(cars.length == 0) {
            return [];
        }

        return cars.sort(
            (firstCar:any, secondCar:any) => {

              const calculateTotal = (car: any) => {

                const protecao = car.VehAvail.VehAvailCore.VehAvailInfo.PricedCoverages.filter(x => x.PricedCoverage.Coverage.Code === 'MB0KM')[0];
                const totalProtecoesA = parseFloat(protecao.PricedCoverage.Charge.Calculation.UnitCharge) * 30;
                const periods = car.VehAvail.VehAvailCore.TotalCharge.Periods;

                return periods[0].valor_mensal + totalProtecoesA;
              }

              return (calculateTotal(secondCar) > calculateTotal(firstCar) ? -1 : 1);
        });
    }
}
