import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Session } from 'src/app/util/session.service';
import { MainSharedService } from 'src/app/util/shared.service';
//import { MenuService } from './header/menu.service';

@Injectable({
  providedIn: 'root'
})

export class CheckLoginService {
  constructor(
    private router: Router,
    private shared: MainSharedService,
    private storage: Session
  ) { }

  removeStorageInfos() {
    localStorage.removeItem('_token');
    localStorage.removeItem('_user');
    localStorage.removeItem('_ondemand');
    localStorage.removeItem('_infoPlate');
    this.storage.remove('ondemand')
    this.storage.remove('vehicleDetails')
    this.storage.remove('user')
    this.storage.remove('infoPlate')
    this.shared.onDemand = null
    this.shared.vehicleDetails = null
    this.shared.infoPlate = null
    this.shared.user = null;
  }

  check() {
    const user = JSON.parse(localStorage.getItem('_user'))
    const token = JSON.parse(localStorage.getItem('_token'))

    const userHasSeller = user && user.VendedorPessoaID != undefined  && user.VendedorPessoaID != "" ? true : false;
    this.shared.userHasSeller = userHasSeller
    setTimeout(() => {
      if (token === null) {
        if (user && user.hasOwnProperty('isJobAuth')) {
          this.removeStorageInfos()
          this.shared.setUsuarioLogado();
          this.shared.reserveToken();
          this.router.navigate(["/"]);
        }
        return
      }
    });
  }
}
