import {Component, OnInit, Input} from '@angular/core';
import { PagamentosReciboService } from './pagamentos-recibo.service';
import { DownloadPdfService } from '../download-pdf/download-pdf.service';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'movida-pagamentos-recibo',
  templateUrl: './pagamentos-recibo.component.html',
  styleUrls: ['./pagamentos-recibo.component.scss']
})

export class PagamentosReciboComponent implements OnInit {

  public data: any = [];
  public ContratoNro = '???';
  public DataCobranca = '??/??/????';
  public Mensalidade = '??';
  public Sequencia = '??';
  public Cartao = '???';
  public Valor = '???';
  public CustomerReceipt: any;
  public BandeiraID: number;
  public DownloadLink: SafeUrl;
  public ValorTotal = 0;
  public ValorTotalFormatado:string;
  public ValorMensalidade = 0;
  public ValorMensalidadeFormatado:string;
  public hasSemParar = false;
  public SemParar : object;
  public UtilizacoesSemParar : object;
  public totalSemParar = new Intl.NumberFormat('pt-BR', {
    style: 'currency',
    currency: 'BRL'
  }).format(parseFloat('0'));
  @Input() reciboToken;
  Recibos: any;

  constructor(
    public service: PagamentosReciboService,
    public pdf: DownloadPdfService,
    public sanitizer: DomSanitizer
  ) {

  }

  ngOnInit() {
    this.getReciboDecoded(this.reciboToken);
  }

  getReciboDecoded(token) {
    this.service.getReciboDecoded(token).subscribe(res => {
      this.ContratoNro = res['ContratoNro'];
      this.Mensalidade = res['Mensalidade'].padStart(2, '0');
      this.Sequencia = res['Mensalidade'];
      this.DataCobranca = res['DataCobranca'];

      this.Recibos = res['Recibos'];
      if (Object.keys(this.Recibos).length > 0) {
        Object.keys(this.Recibos).map((k) => {
          this.Recibos[k]['ValorTransacao'] = new Intl.NumberFormat('pt-BR', {
            style: 'currency',
            currency: 'BRL'
          }).format(parseFloat(this.Recibos[k]['ValorTransacao']));
          if(typeof this.Recibos[k]['ValorMensal'] !== 'undefined') {
            this.ValorMensalidade = parseFloat(this.Recibos[k]['ValorMensal']);
            this.ValorMensalidadeFormatado = new Intl.NumberFormat('pt-BR', {
              style: 'currency',
              currency: 'BRL'
            }).format(this.ValorMensalidade);

            if (this.isValidSemParar(res)) {
              this.SemParar = res['SemParar'];
              this.sumSemParar(this.SemParar['utilizacoes']);
              this.adjustUtilizacoesSemParar();
              this.UtilizacoesSemParar = this.SemParar['utilizacoes'];
            } else {
              this.ValorTotalFormatado = this.ValorMensalidadeFormatado;
            }
          }
        });
      }
      this.service.getReciboPdf(this.reciboToken).subscribe(result => {
        const base64String = result['file'] ? result['file'] : null;
        this.pdf.getLinkPdf(base64String, `recibo_${this.ContratoNro}_${this.Mensalidade}`)
          .then((link) => {
            this.DownloadLink = this.sanitizer.bypassSecurityTrustUrl(link.toString());
          })
          .catch(e => {
            //
          });
      });
    }, ex => {
    });
  }

  adjustUtilizacoesSemParar(){
    let i = 0;
    Object.keys(this.SemParar['utilizacoes']).map((k) => {
      let timestamp = Date.parse(this.SemParar['utilizacoes'][k]['dataHora']);
      let date = new Date(timestamp);
      this.SemParar['utilizacoes'][k]['data'] = this.formatDate(date);
      this.SemParar['utilizacoes'][k]['order'] = i;
      this.SemParar['utilizacoes'][k]['valorFormatado'] = new Intl.NumberFormat('pt-BR', {
        style: 'currency',
        currency: 'BRL'
      }).format(parseFloat(this.SemParar['utilizacoes'][k]['valor']));
      i++;
    })
  }

  formatDate(date) {

    let day = date.getDay().toString().padStart(2, '0');
    let month = date.getMonth().toString().padStart(2, '0');
    let year = date.getFullYear();
    let hour = date.getHours().toString().padStart(2, '0');
    let minutes = date.getMinutes().toString().padStart(2, '0');

    return day + '/' + month + '/' + year + ' ' + hour + ':' + minutes;

  }

  sumSemParar(utilizacoes){

    let total = 0;
    Object.keys(utilizacoes).map((k) => {
      total = total + parseFloat(utilizacoes[k].valor);
    });
    this.ValorTotal = this.ValorMensalidade + total;

    this.ValorTotalFormatado = new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(this.ValorTotal);

    this.totalSemParar = new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(total);
  }

  isValidSemParar(payload){
    if (typeof payload['SemParar'] !== 'undefined' && Object.keys(payload['SemParar']).length > 0) {
      let SemParar = payload['SemParar'];

      if (typeof SemParar['sticker'] !== 'undefined' && typeof SemParar['placaVeiculo'] !== 'undefined' &&  typeof SemParar['utilizacoes'] !== 'undefined') {
        this.hasSemParar = true;
        return true;
      }
    }
    return false;
  }
}
