import { Injectable } from '@angular/core';
import {
    HttpInterceptor
    //, HttpErrorResponse
    , HttpEvent
    , HttpRequest
    , HttpHandler
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { _throw } from "rxjs/observable/throw";
import { EmptyObservable } from 'rxjs/observable/EmptyObservable';
import 'rxjs/add/operator/catch';
import { MainSharedService } from 'src/app/util/shared.service';
import { AlertService } from 'src/app/core/services/alert.service';

@Injectable({
    providedIn: 'root'
})
export class ChaordicInterceptor implements HttpInterceptor {
    constructor(//private injector: Injector,
        private shared: MainSharedService,
        private alertService: AlertService
    ) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(request).catch(error => {
            let er = error.error;
            if (error.status == 404 && (er.code === 3001 || er.message === 'Query not found' || er === null || er === undefined)) {
                this.shared.setLoadingAtivo(false);
                this.alertService.open({
                    tipo: 'message',
                    titulo: 'Desculpe, no encontramos nenhum veculo',
                    botao: 'Ok',
                    mensagem: 'Por favor, inicie uma nova busca.'
                }, true);
                return new EmptyObservable();
            }
            else
                return _throw(error);
        });
    }
}