import { Injectable } from '@angular/core';
import { GlobalMessage } from 'src/app/shared/alert-message/alert-message.component';
import { MatDialog } from '@angular/material/dialog';

export type IType = 'message' | 'redirect' | 'openRegionModal';

export interface IMatParam {
  tipo: IType;
  titulo;
  botao;
  mensagem;
}

@Injectable({
  providedIn: 'root'
})
export class AlertService {
  constructor(private alert: MatDialog) {}

  open(params: IMatParam, disableClose: boolean) {
    this.alert.open(GlobalMessage, { data: params, disableClose });
  }
}
