import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import {
    API_GET_TARIFF,
   // API_QUOTE_RESERVATION,
    API_CHECKOUT_CARD,
    API_CHECKOUT_RESERVE,
    APP_NAME, API_CHECKOUT_SEND_CARD, API_CREDIT_ANALYSIS, API_NEW_CONTRACT, API_QUOTE_TARIFF, API_VALIDATE_PAYER
} from 'src/app/core/api/app.api';
import { Injectable } from '@angular/core';
import { LoginService } from './login.service';
import { throwError as observableThrowError } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { MainSharedService } from 'src/app/util/shared.service';
import CryptoJS from 'crypto-js'

@Injectable({
    providedIn: 'root'
})
export class CheckoutService {

    constructor(
        private http: HttpClient,
        private loginService: LoginService,
        private shared: MainSharedService
    ) { }

    errorHandler(error: HttpErrorResponse) {
        return observableThrowError(error.message || "Server Error");
    }

    getCardBrand(card): Observable<any> {
        return this.http.get<any>(API_CHECKOUT_CARD + '?card=' + card);
    }

    getCreditAnalysis(orderId): Observable<any> {
        return this.http.get<any>(API_CREDIT_ANALYSIS + `/${orderId}`);
    }

    getNewContract(data): Observable<any> {
        return this.http.post<any>(API_NEW_CONTRACT, data);
    }

    savePaymentData(pessoa_id, payment) {
        return this.http.post<any>(API_CHECKOUT_SEND_CARD, payment);
    }

    saveReserve(reserve) {
        return this.http.post<any>(API_CHECKOUT_RESERVE, reserve);
    }

    sendProduct(id) {
        let param = this.getObjeto(id);
        let reqHeader = new HttpHeaders({
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + this.loginService.getUserToken()
        });
        return this.http.post(API_CHECKOUT_SEND_CARD, JSON.stringify(param), {
            headers: reqHeader
        });
    }

    getObjeto(id) {
        let x = [{
            'item_id': id,
            'protecoes': [
                { 'id': '1' }
            ],
            'protecaoextra': [
                { 'id': 5 },
                { 'id': 49 },
                { 'id': 71 }
            ],
            'servicosadicionais': [
                { 'id': 24 },
                { 'id': 26 }]
        }];
        return x;
    }

    getHeadersCard() {
        return new HttpHeaders({
            'api-token': '8fc8714e4514d3f015210573990ebef5',
            'aplicationname': APP_NAME,
            'Content-Type': 'application/json'
        });
    }

    encryptTotalPrepagamentoPay(total: number) {
        const CryptoJSAesJson = {
            stringify: (cipherParams: any) => {
                let j: any = { ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64) };
                if (cipherParams.iv) j.iv = cipherParams.iv.toString();
                if (cipherParams.salt) j.s = cipherParams.salt.toString();
                return JSON.stringify(j);
            },
            parse: (jsonStr: any) => {
                let j: any = JSON.parse(jsonStr);
                let cipherParams: any = CryptoJS.lib.CipherParams.create({ ciphertext: CryptoJS.enc.Base64.parse(j.ct) });
                if (j.iv) cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv)
                if (j.s) cipherParams.salt = CryptoJS.enc.Hex.parse(j.s)
                return cipherParams;
            }
        }

        this.shared.totalEncrypted = CryptoJS.AES.encrypt(JSON.stringify(total), this.shared.siteKey, { format: CryptoJSAesJson }).toString();

    }

    getTariff(fipe, mileage, period, userType, promoCode = null) {
        const promoCodeParam = promoCode ? `?promotional-code=${promoCode}` : '';
        return this.http.get<any>(`${API_GET_TARIFF}/${fipe}/${mileage}/${period}/${userType}${promoCodeParam}`);
    }

    quoteReservation(bookingData: any) {
        let bookingHeaders = new HttpHeaders().append('Content-Type', 'application/json');
        // const url = `${this.apiEndpoint}/booking/quote-tariff`;
        return this.http.post<any>(API_QUOTE_TARIFF, bookingData, { headers: bookingHeaders }).pipe(tap(data => catchError(this.errorHandler)));
    }

    verifyPayer(orderId: Number) {
        let data = {
            pedido_id: orderId
        }
        return this.http.post<any>(`${API_VALIDATE_PAYER}`, data).pipe(tap(data => catchError(this.errorHandler)));
    }

}
