  getOrderOnDemandPayload(formOnDemand, vehicle, tokenVendedor, utmData) {
    const coupon = localStorage.getItem('CPN');
    const data = {
      vendedor_link: tokenVendedor,
      vendedor_id: vehicle.sellerCode ? vehicle.sellerCode : null,
      nome: formOnDemand.nome,
      cpf: formOnDemand.cpf,
      telefone: formOnDemand.telefone,
      email: formOnDemand.email,
      cidade: '',
      estado: '',
      mensagem: formOnDemand.mensagem,
      local_retirada: formOnDemand.filialSigla,
      local_devolucao: formOnDemand.filialSigla,
      datahora_retirada_prevista: vehicle.bookingPayload.data_retirada,
      data_retirada: vehicle.bookingPayload.data_retirada.split()[0],
      data_devolucao: vehicle.bookingPayload.data_devolucao.split()[0],
      coupom: coupon ? coupon : null,
      status_validate_selfie: 1,
      status_validate_cnh: 1,
      sobdemanda: true,
      token_cnh: 'null',
      codigo_veiculo: vehicle.acriss,
      condutores: JSON.parse(localStorage.getItem('condutores')),
      protecoes: {
        sigla: vehicle.protection.sigla
      },
      equipamentos: vehicle.bookingPayload.equipamentos,
      vehicle_details: {
        group: vehicle.groupId,
        acriss: vehicle.acriss,
        fipe: vehicle.fipe,
        modelo: vehicle.version,
        pacoteKM: vehicle.mileage.valor.valor,
        tempoContrato: vehicle.mileage.valor.meses,
        cor_id: null,
        plate: null
      },
      tariff: {
        collection_day: '25',
        tariff: vehicle.mileage.valor.sigla
      },
      bitrix: {
        acao: formOnDemand.bitrix,
        fonte: formOnDemand.fonte
      },
      utm: utmData,
    }
    return data;
  }

  protected twentyMore(vehicle) {
    var dataPrevistaTemp = new Date(), dataPrevistaNew = new Date(), diaPrevista = '', mesAtual = '',
        mesAtual = '', mesPrevista = '', mesPrevista = '', anoPrevista = 0, data = '',
        dataPrevistaRetirada = vehicle.color.placas[0].PrazoEntrega == '20 Dias' ? 20 : 0,
        dataPrevista = vehicle.bookingPayload.data_retirada,
        dataTemp = dataPrevista.split("/"),
        ano = dataTemp[2].split(" "),
        mes = dataTemp[1]-1,
        dia = dataTemp[0];
        ano = ano[0];

        dataPrevistaTemp = new Date(ano,mes,dia);
        dataPrevistaNew = new Date(dataPrevistaTemp);
        dataPrevistaNew.setDate(dataPrevistaNew.getDate() + dataPrevistaRetirada);
        dia  = dataPrevistaNew.getDate().toString();
        diaPrevista = (dia.length == 1) ? '0'+dia : dia;
        mesAtual  = (dataPrevistaNew.getMonth()+1).toString();
        mesPrevista = (mesAtual.length == 1) ? '0'+mesAtual : mesAtual;
        anoPrevista = dataPrevistaNew.getFullYear();
        data =  diaPrevista+"/"+mesPrevista+"/"+anoPrevista+" 10:00";
    return data;
  }

