  public getChecklist(data) {
    return this.http.get(`${API_CHECKLIST_ORDER}?Plate=${data.Plate}&IsSeller=${data.IsSeller}`).pipe(
      map(res => {
        return res
      }), catchError(() => of(null))
    )
  }

  public getOrder(orderId): Observable<any> {
    return this.http.get(`${API_ORDERS}/${orderId}`).pipe(
      map((res: any) => {

        if (typeof res.data.pedido !== 'undefined') {
          localStorage.setItem('pagadorID', res.data.pedido.PagadorID);
          return res.data;
        }

        return false;
      }), catchError(() => of(null))
    );
  }

  public getSellerOrder(orderId): Observable<any> {
    return this.http.get(`${API_SELLER_ORDERS}/${orderId}`).pipe(
      map((res: any) => {

        return res.data;
      }), catchError(() => of(null))
    );
  }

  getOrders(pageNumber) {
    //const page = pageNumber && pageNumber !== 1 ? `?page=${pageNumber}` : '';
    // return this.http.get<any>(`${API_ORDERS}${page}`);
    return this.http.get<any>(`${API_ORDERS}`);
  }

  getSellerOrders(pessoaId, pageNumber) {
    //const page = pageNumber && pageNumber !== 1 ? `?page=${pageNumber}` : '';
    // return this.http.get<any>(`${API_SELLER_ORDERS}${page}`);
    return this.http.get<any>(`${API_SELLER_ORDERS}/${pessoaId}?PessoaID=${pessoaId}`);
  }

  create(isOnDemand, user, vehicle, tokenVendedor, utmData) {
    return this.http.post<any>(`${API_ORDER}/create`, this.getOrderPayload(isOnDemand, user, vehicle, tokenVendedor, utmData));
  }

  createOnDemand(formOnDemand, vehicle, tokenVendedor, utmData) {
    const header = {
      headers: new HttpHeaders().delete('UserToken')
    }
    return this.http.post<any>(`${API_ORDER}/createOnDemand`, this.getOrderOnDemandPayload(formOnDemand, vehicle, tokenVendedor, utmData), header);
  }

  createLeadTakeFromAnotherStore(formOnDemand, vehicle, tokenVendedor, utmData) {
    const header = {
      headers: new HttpHeaders().delete('UserToken')
    }
    return this.http.post<any>(`${API_LEAD}`, this.getOrderOnDemandPayload(formOnDemand, vehicle, tokenVendedor, utmData), header);
  }

  createLandingPage(data) {

    return this.http.post<any>(`${API_LANDING_PAGE}`, data);
  }
