import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {DOCUMENT} from '@angular/common';
import {MenuService} from 'src/app/core/services/header/menu.service';
import {MainSharedService} from 'src/app/util/shared.service';
import {AlertMessageComponent} from 'src/app/shared/alert-message/alert-message.component';
import {SellerService} from 'src/app/core/services/seller.service';
import {GtmService} from 'src/app/core/services/gtm.service';
import {OrderService} from 'src/app/core/services/order.service';
import {environment} from 'src/environments/environment';
//import {EventBusService} from 'src/app/core/services/eventbus.service';
import {SubSink} from 'subsink';
//import CryptoJS from 'crypto-js';
import {CheckLoginService} from 'src/app/core/services/checklogin.service';
import {UserService} from 'src/app/core/services/user.service';
import { Crypter } from 'src/app/util/cripto.util';
import { LocalStorage } from 'src/app/util/local-storage.service';

@Component({
  selector: 'app-login-button',
  templateUrl: './login-button.component.html',
  styleUrls: ['./login-button.component.scss'],
  providers: [AlertMessageComponent],
})

export class LoginButtonComponent implements OnInit, OnDestroy {

  redirectToParams: any;
  public registerLink: any;
  private sub = new SubSink();
  private triggerAlert = true;

  public onDemand = false;

  public user: any;

  // Screen
  public hiddenScreen: any;
  public onchangeScreen: any;

  private defaultUtm = {
    utm_source: '',
    utm_medium: '',
    utm_campaign: '',
    utm_content: '',
    utm_term: '',
  }

  constructor(
    private router: Router,
    public shared: MainSharedService,
    private gtm: GtmService,
    private orderS: OrderService,
    private userS: UserService,
    private sellerS: SellerService,
    private alertMsg: AlertMessageComponent,
    private menuService: MenuService,
    //private eventBusService: EventBusService,
    private checkLoginService: CheckLoginService,
    private localStorage: LocalStorage,
    @Inject(DOCUMENT) private document: Document
  ) {
  }

  ngOnInit() {

    // iPad on iOS 13 detection
    ['iPad Simulator', 'iPhone Simulator', 'iPod Simulator', 'iPad', 'iPhone', 'iPod'].includes(navigator.platform) ||
    (navigator.userAgent.includes('Mac') && 'ontouchend' in document) ? this.shared.isIphone = true : this.shared.isIphone = false;

    this.shared.reserveToken();
  }

  loginClicked() {
    this.shared.loginClicked = true;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  remoteLogin(seller: boolean = false) {

    const showLoading = (show: boolean) => {
      this.shared.setLoadingAtivo(show);
    };



    const redirectTo = (url: string) => {
      this.document.location.href = url;
    };

    const openDialog = () => {

      this.alertMsg.openDialog({
        icon: 'attention.svg',
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: 'Recarrega a pgina e tente novamente.'
      }, 'message', true);

    };

    showLoading(true);

    this.menuService.reserveToken().subscribe({
      next(response: any) {

        if (response.data) {

          const token = response.data;

          const loginURL = `${environment.siteMovida}login/zkm/${token}?seller=${seller}`;

          redirectTo(loginURL);

        } else {

          openDialog();
        }
      },
      complete() {
        showLoading(false);
      },
      error(msg) {
        console.log('Error => ', msg);
      }
    });
  }

  getLogin(token) {
    this.sub.sink = this.menuService.getInfo(token).subscribe(response => {
      if(typeof response === 'object' && typeof response['success'] !== 'undefined' && typeof response['data'] === 'object') {
        if (response['success'] && response['data']['data'] !== 'token reservado' && !response['data'].hasOwnProperty('isJobAuth')) {
          this.shared.removeToken();
          this.shared.removeLoginClicked();
          this.submit(response, token);
          // this.eventBusService.emit(new EmitEvent(Events.loginIOS, response['data']));
          if (localStorage.getItem('redirectTo_rf') !== null) {
            this.router.navigate([localStorage.getItem('redirectTo_rf')]);
            this.shared.removeRedirectToRF()
          } else {
            if (response['data'].RF && response['data'].RF.pessoaID) {
              this.showAttention(`/assinatura/responsavel/pagamento/${response['data'].RF.pedidoFrotaID}`, response['data'].RF);
            }
          }
          this.checkLoginService.check()
        }
      }
    });
  }

  showAttention(url, pedido) {
    if (this.triggerAlert) {
      this.shared.setLoadingAtivo(false);
      this.triggerAlert = false;
      this.alertMsg.openDialog({
        icon: 'attention.svg',
        tipo: 'rf',
        url,
        titular: pedido.razaoSocialTitular,
        pedido: pedido.pedidoFrotaID,
        titulo: 'Pedido em aberto'
      }, 'rf', true);
    }
  }

  redirectToVehicleDetails() {
    const url = this.shared.isOnDemand ? 'sob-demanda' : 'detalhes';
    const brand = this.shared.vehicleDetails.brand.replace(/ /g, '');
    const model = this.shared.vehicleDetails.model.replace(/ /g, '');
    const fipe = this.shared.vehicleDetails.fipe;
    // tslint:disable-next-line:max-line-length
    this.router.navigate([`/assinatura/${url}/${brand.replace(/ /g, '')}/${model.replace(/ /g, '')}/${fipe}`], {queryParamsHandling: 'merge'});
  }

  createSellerLink() {

    this.shared.setCounterLoading(true);
    const vehicle = this.shared.vehicleDetails;
    try {
      this.lockVehicleAndCreateLink(vehicle);
    } catch (err) {
      this.shared.setCounterLoading(false);
      const msg = 'No foi possvel gerar o link. Tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: msg
      }, 'message', true);
    }
  }

  lockVehicleAndCreateLink(vehicle) {

    if (this.onDemand && this.shared.choosenView === 'seller' || this.shared.otherPlaceSelected && this.shared.choosenView === 'seller') {
      const data = {
        vendedor: this.shared.user.PessoaID,
        nome: this.shared.onDemand.nome,
        cpf: this.shared.onDemand.cpf,
        cidade: this.shared.onDemand.cidade,
        estado: this.shared.onDemand.estado,
        email: this.shared.onDemand.email,
        telefone: this.shared.onDemand.telefone,
        mensagem: this.shared.onDemand.mensagem,
        on_demand: true,
        veiculo: {
          nome: this.shared.carSlug,
          fipe: vehicle.fipe
        },
        periodo: {
          meses: vehicle.period.meses,
          franquia: vehicle.mileage.valor.valor,
          sigla: vehicle.mileage.valor.sigla
        },
      };
      const token = Crypter.encrypter(data);
      this.sellerS.createSellerLink(token).subscribe(resToken => {
        this.shared.setCounterLoading(false);
        this.router.navigate([`/vendedor/link/${resToken.data.hash}`]);
        return;
      });
    } else {
      this.sellerS.lockVehicle(vehicle.plate.info.Placa).subscribe(res => {
        if (res.success) {
          const data = {
            vendedor: this.shared.user.PessoaID,
            veiculo: {
              nome: this.shared.carSlug,
              fipe: vehicle.fipe,
              groupId: vehicle.groupId
            },
            loja: {
              FilialID: vehicle.pickupStore.FilialID,
              CEP: vehicle.pickupStore.CEP,
              Loja: vehicle.pickupStore.Loja,
              Sigla: vehicle.pickupStore.Sigla
            },
            cor: {
              CorID: vehicle.color.CorID,
              Cor: vehicle.color.Cor,
              Hexadecimal: vehicle.color.Hexadecimal
            },
            placa: {
              FrotaID: vehicle.plate.info.FrotaID,
              Placa: vehicle.plate.info.Placa,
              StatusID: vehicle.plate.info.StatusID
            },
            protecao: {
              sigla: vehicle.protection.sigla,
              nome: vehicle.protection.nome,
              descricao: vehicle.protection.descricao,
              valor: vehicle.protection.valor
            },
            periodo: {
              meses: vehicle.period.meses,
              franquia: vehicle.mileage.valor.valor,
              sigla: vehicle.mileage.valor.sigla
            },
            on_demand: this.onDemand ? true : false
          };

          const token = Crypter.encrypter(data);
          this.sellerS.createSellerLink(token).subscribe(resToken => {
            this.shared.setCounterLoading(false);
            this.router.navigate([`/vendedor/link/${resToken.data.hash}`]);
            return;
          });
        } else {
          this.shared.setCounterLoading(false);
          const msg = 'No foi possvel gerar o link. Tente novamente mais tarde.';
          this.alertMsg.openDialog({
            tipo: 'message',
            titulo: 'Algo deu errado!',
            mensagem: msg
          }, 'message', true);
        }
      }, ex => {
        this.shared.setCounterLoading(false);
        const msg = 'No foi possvel gerar o link. Tente novamente mais tarde.';
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: msg
        }, 'message', true);
      });
    }
  }

  createOrder() {
    this.shared.setLoadingAtivo(true);
    let utmData = this.defaultUtm;
    if (this.localStorage.get('utm')) {
      utmData = JSON.parse(this.localStorage.get('utm'));
    }

    this.orderS.create(this.shared.isOnDemand, this.shared.user, this.shared.vehicleDetails, this.shared.tokenVendedor, utmData).subscribe(res => {
      if (res !== undefined && res != null && res && res.success) {
        localStorage.removeItem('condutores');
        this.gtm.addOrderGTM('order', false, res.data.id, this.shared.vehicleDetails);
        this.shared.setLoadingAtivo(false);
        this.router.navigate([`/assinatura/pagamento/${res.data.PedidoFrotaID}`]);
        return;
      }
      this.shared.setLoadingAtivo(false);
      const msg = 'No foi possvel gerar o seu pedido. Tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: msg
      }, 'message', true);
      this.redirectToVehicleDetails();
    }, ex => {
      this.shared.setCounterLoading(false);
      const type = ex.error.data.msg.includes('Bloqueado ou Inativo!') ? 'blockedUser' : 'message';
      const msg = 'No foi possvel gerar o seu pedido. Tente novamente mais tarde.';
      this.alertMsg.openDialog({
        titulo: 'Ops! Algo deu errado.',
        mensagem: msg,
        tipo: 'message',
      }, type, true);
      this.redirectToVehicleDetails();
    });
  }

  showErrorAlert(msg = null) {
    if (this.triggerAlert) {
      this.shared.setLoadingAtivo(false);
      this.triggerAlert = false;
      const message = msg ? msg : 'No foi possvel carregar as informaes do veculo. Por favor, tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'redirect',
        titulo: 'Desculpe,',
        mensagem: message
      }, 'redirect', true);
    }
  }

  public submit(userData, token) {
    this.shared.setLoadingAtivo(true);
    this.shared.user = {
      ...userData.data,
      access_token: token,
      vendedor_autorizado: userData.data.VendedorID !== ''
    };

    this.shared.isSeller = this.shared.user.vendedor_autorizado;
    this.shared.choosenView = this.shared.user.vendedor_autorizado ? 'seller' : 'public';

    this.shared.setUsuarioLogado();
    this.userS.mySeller();

    if (this.shared.redirectTo && this.shared.redirectTo === 'assinatura/pagamento') {
      this.shared.setLoadingAtivo(false);
      if (this.shared.choosenView === 'seller') {
        this.createSellerLink();
        return;
      }
      let data = {};
      if (this.shared.loadSellerPreSelection) {
        data = {
          Plate: this.shared.sellerPreSelection.placa.Placa,
          IsSeller: true
        };
      } else {
        data = {
          Plate: this.shared.selectedPlate ? this.shared.selectedPlate.info.Placa : this.shared.vehicleDetails?.color.placas[0].Placa,
          IsSeller: false
        };
      }
      this.orderS.getChecklist(data).subscribe(res => {
        this.shared.setLoadingAtivo(false);
        if (res.data && (!res.data.hasOrder && !res.data.isPlateAvailable || res.data.hasOrder && !res.data.isPlateAvailable)) {
          this.showErrorAlert('O veculo encontra-se indisponvel. Tente novamente mais tarde.');
        } else {
          if (res.data && (res.data.hasOrder && res.data.isPlateAvailable || res.data.hasOrder && !res.data.isPlateAvailable)) {
            this.alertMsg.openDialog({
              icon: 'car-icon.svg',
              tipo: 'message',
              titulo: 'Novo pedido',
              mensagem: 'Voc j tem um pedido conosco. Caso deseje continuar, ' +
                'ser gerada uma nova cobrana em seu carto. Deseja continuar?'
            }, 'question', true).then(dialogRef => {
              this.sub.sink = dialogRef.componentInstance.yes.subscribe(() => {
                this.createOrder();
              });
              this.sub.sink = dialogRef.componentInstance.not.subscribe(() => {
                this.router.navigate(['/minha-conta']);
              });
              this.sub.sink = dialogRef.componentInstance.close.subscribe(() => {
                this.router.navigate(['/minha-conta']);
              });
            });
          } else {
            this.createOrder();
          }
        }
      });
      return;
    }
    this.shared.setLoadingAtivo(false);
    if (this.shared.redirectTo) {
      this.router.navigate([`/${this.shared.redirectTo}`]);
      return;
    }
  }
}
